let Empty = {type: 'Empty'};
let body = {type: 'BlockStatement', body: [] };
let Identifier = (name) => ({type: 'Identifier', value: name || '' });
let StringLiteral = (value) => ({type: 'StringLiteral', value: value || '' });

let ConditionStatement = {
	type: 'ConditionStatement',
	test: Empty,
	consequent: body,
};
let BinaryExpression = (operator) => ({type: 'BinaryExpression', operator, left: Empty, right: Empty});
let MathFunction = (name) => ({
	type: 'CallExpression',
	callee: {type: 'MemberExpression', object: Identifier('Math'), property: StringLiteral(name) },
	arguments: [Empty],
});
let FunctionExpression = {type: 'FunctionExpression', params: [], body};


function dot(name) {
	return {
		type: 'CallExpression',
		callee: {type: 'MemberExpression', computed:true, object: Empty, property: StringLiteral(name) },
		arguments: [Empty],
	};
}

let samples = {
	'Empty': {type: 'Empty'},
	'String': StringLiteral(),
	'Array': {type: 'ArrayExpression', elements: [] },
	'Object': {type: 'ObjectExpression', properties: [] },
	'Set': { type: 'AssignmentExpression', operator: '=', left: Empty, right: Empty},
	'Print': {type: 'PrintStatement', argument: Empty },
	'CallExpression': {type: 'CallExpression', callee: Empty, arguments: [] },
	'MemberExpression': {type: 'MemberExpression', computed:true, object: Empty, property: Empty },
	'DotExpression': {type: 'MemberExpression', computed:true, object: Empty, property: StringLiteral() },
	'ObjectProperty': {
		type: 'ObjectProperty',
		key: StringLiteral(),
		value: Empty,
	},
	'Itself': {type: 'ItselfLiteral'},
	'Null': {type: 'NullLiteral'},
	'GlobalVariable': {type: 'GlobalVariableStatement', var: Identifier() },
	'Comment': {type: 'Comment', value: ''},
	'Separator': {type: 'Separator', height: 40},
	'If': {type: 'IfStatement', conditions: [ConditionStatement] },
	ConditionStatement,
	'Else': { type: 'ElseStatement', body },
	'ForRangeStatement': {type: 'ForRangeStatement', var: Identifier(), first: Empty, last: Empty, step: null, body},
	'ForOfStatement': {type: 'ForOfStatement', left: Identifier(), right: Empty, body},
	'While': {type: 'WhileStatement', test: Empty, body},
	'Function': FunctionExpression,
	'AsyncFunction': {type: 'FunctionExpression', "async": true, params: [], body},
	'Return': {type: 'ReturnStatement', argument: Empty },
	'Break': {type: 'BreakStatement'},
	'Continue': {type: 'ContinueStatement'},
	'Await': {type: 'AwaitExpression', argument: Empty},
	'Async': {type: 'Async'},
	'New': {type: 'NewExpression', callee: Empty, arguments: [] },
	'Includes': dot('includes'),
	'Push': dot('push'),
	'Slice': dot('slice'),
	'Map': dot('map'),
	'Filter': dot('filter'),
	'IndexOf': dot('indexOf'),
	'Length': {type: 'MemberExpression', computed:true, object: Empty, property: StringLiteral("length") },

	'Sqrt': {type: 'RootExpression', argument: Empty },
	'Mod': {type: 'BinaryExpression', operator: '%', left: Empty, right: Empty},
	'Increment': {
		type: 'AssignmentExpression',
		operator: '=',
		left: Empty,
		right: {
			type: 'BinaryExpression',
			operator: '+',
			left: {type: 'ItselfLiteral'},
			right: Empty,
		}
	},
	'IncrementBy1': {
		type: 'AssignmentExpression',
		operator: '=',
		left: Empty,
		right: {
			type: 'BinaryExpression',
			operator: '+',
			left: {type: 'ItselfLiteral'},
			right: {type:'NumericLiteral', value: 1},
		}
	},
	'Random': {
		type: 'CallExpression',
		callee: {type: 'MemberExpression', object: Identifier('Math'), property: StringLiteral("random") },
		arguments: [],
	},
	'PI': {
		type: 'MemberExpression',
		object: Identifier('Math'),
		property: StringLiteral("PI"),
	},
	'Floor': MathFunction('floor'),
	'AbsoluteValue': MathFunction('abs'),
	'Sin': MathFunction('sin'),
	'Cos': MathFunction('cos'),
	'And': {type: 'LogicalExpression', operator: '&&', left: Empty, right: Empty },
	'Or': {type: 'LogicalExpression', operator: '||', left: Empty, right: Empty },
	'Not': {type: 'UnaryExpression', operator: '!', argument: Empty },
	'True': {type: 'BooleanLiteral', value: true},
	'False': {type: 'BooleanLiteral', value: false},
	'=':  BinaryExpression('=='),
	'!=': BinaryExpression('!='),
	'>':  BinaryExpression('>'),
	'>=': BinaryExpression('>='),
	'<':  BinaryExpression('<'),
	'<=': BinaryExpression('<='),
	'+': BinaryExpression('+'),
	'-': BinaryExpression('-'),
	'*': BinaryExpression('*'),
	'/': BinaryExpression('/'),
	'^': BinaryExpression('**'),
	'Typeof': {type: 'UnaryExpression', operator: 'typeof', argument: Empty },
	'Delete': {type: 'UnaryExpression', operator: 'delete', argument: Empty },
	'This': {type: 'ThisExpression'},
	'setTimeout': {
		type: 'CallExpression',
		callee: Identifier('setTimeout'),
		arguments: [ FunctionExpression, {type: 'NumericLiteral', value: 1000} ]
	},
	'setInterval': {
		type: 'CallExpression',
		callee: Identifier('setInterval'),
		arguments: [ FunctionExpression, {type: 'NumericLiteral', value: 1000} ]
	},
	'Try' : {
		type: 'TryStatement',
		block: body,
		handler: {param: Identifier('err'), body },
	},
	'...': { type: 'SpreadElement', argument: Empty },
};

for (let P of Object.values(samples))  {
	 P.is_sample = true;
	 P.focus = {child: 'start'};
}

let panel_samples = [
	['Empty', 'String', 'Array', 'Object', 'Set', 'Print', '=', 'CallExpression', 'MemberExpression', 'Itself', 'Null', 'GlobalVariable', 'Comment', 'Separator'],
	['If', 'ForRangeStatement', 'ForOfStatement', 'While', 'Break', 'Continue', 'Try'],
	['Array', 'Length', 'Includes', 'Push', 'Slice', 'Map', 'Filter', 'IndexOf' ],
	['Function', 'Return', 'New', 'Await', 'Async', 'setTimeout', 'setInterval'],
	['+', '-', '*', '/', '=', '^', 'Sqrt', 'Mod', 'IncrementBy1', 'Random', 'Floor', 'AbsoluteValue', 'PI', 'Sin', 'Cos'],
	['And', 'Or', 'Not', 'True', 'False', '=', '!=', '>', '>=', '<', '<=']
];

for (let i=0; i < panel_samples.length; i++) {
	panel_samples[i] = panel_samples[i].map(s => samples[s] );
}

samples.panel_samples = panel_samples;

export default samples
