import Vue from 'vue'
import App from './App.vue'
import router from './router'
import vuetify from './plugins/vuetify';

/*
var nerdamer = require('nerdamer');
window.N = nerdamer;
window.P = nerdamer.getCore().PARSER;
*/

import Piece from '@/components/Piece';
Vue.component('Piece', Piece );

Vue.config.productionTip = false

import { mdiMenuRight, mdiUndo, mdiRedo, mdiContentCopy, mdiContentCut, mdiDelete, mdiMessageProcessing, mdiCancel, mdiVectorSelection, mdiSemanticWeb, mdiFolderOpen, mdiAppsBox } from '@mdi/js'
let mdi = { mdiMenuRight, mdiUndo, mdiRedo, mdiContentCopy, mdiContentCut, mdiDelete, mdiMessageProcessing, mdiCancel, mdiVectorSelection, mdiSemanticWeb, mdiFolderOpen, mdiAppsBox };

Vue.prototype.$mdi = {};

for (let icon_name in mdi) {
	Vue.prototype.$mdi[icon_name.slice(3)] = mdi[icon_name];
	delete mdi[icon_name];
}


new Vue({
  router,
  vuetify,
  render: h => h(App)
}).$mount('#app')
