  <v-btn :small="$vuetify.breakpoint.mobile" icon @mousedown="set_mode('copy')"><v-icon :color="$data.$g.selected_pieces.length && common.paste_mode == 'copy' ? 'green':'white' ">
  	{{ $mdi.ContentCopy }}
  </v-icon> </v-btn>
  <v-btn :small="$vuetify.breakpoint.mobile" icon @mousedown="set_mode('cut')"><v-icon :color="$data.$g.selected_pieces.length && common.paste_mode == 'cut' ? 'green':'white' ">
  	{{ $mdi.ContentCut }}
  </v-icon> </v-btn>
  <v-btn :small="$vuetify.breakpoint.mobile" icon @mousedown="remove"> <v-icon> {{ $mdi.Delete }} </v-icon> </v-btn>
  <v-btn :small="$vuetify.breakpoint.mobile" icon @mousedown="select_many"> <v-icon :color="common.select_many ? 'green':'white' ">
  	{{ $mdi.VectorSelection }} 
  </v-icon></v-btn>
  <v-btn :small="$vuetify.breakpoint.mobile" icon @mousedown="common.toggle_disabled()"> <v-icon> {{ $mdi.Cancel }} </v-icon> </v-btn>

  <v-btn v-if="$vuetify.breakpoint.smAndUp" color="green" @mousedown="run"> <v-icon large> {{ $mdi.MenuRight }}  </v-icon> Run </v-btn>
</v-app-bar>

<div class="d-flex">
	<Panel v-if="persistent_panel" />

	<div class="program">
		<div class="code pa-4">
			<Piece :P="ast.program" />
		</div>
		<v-btn @mousedown="run"> Run </v-btn>
		<v-btn @mousedown="save"> Save </v-btn>
		<v-btn @mousedown="js_code_dialog=true; generate();"> Javascript </v-btn>
		<input @change="handle_file_input" type="file" />
		<div class="console" ref="console" >
			<div v-for="line in console" :class="line.type">
				<Piece v-if="line.ast" :P="line.ast.program"> </Piece>
				<div v-else> {{ line.content }} </div>
			</div>
		</div>
	</div>
</div>

<v-menu
	v-model="$menu.showMenu" absolute
	:position-y="$menu.menu_position.y"
	:position-x="$menu.menu_position.x"
	@blur="$menu.under_menu=null"
	@contextmenu.prevent
  >
	<v-card>
	  <div
	  	class="menu_option"
	  	@contextmenu.prevent
	  	@mousedown="$handle_menu_click(item)"
	  	v-for="item in menu_items">
	  	{{ item }}
	  </div>
	</v-card>
</v-menu>


<v-menu
	v-model="common.show_keyword_menu"
	v-if="common.el_with_keyword_menu && common.el_with_keyword_menu.keyword_options && common.el_with_keyword_menu.keyword_options.length"
	:value="true"
	allow-overflow

	:activator="common.el_with_keyword_menu.$el"
	nudge-bottom="25"
	@contextmenu.prevent
  >
	<v-list  class="keyword_menu">
		<v-list-item 
			active="menu_option_active"
			v-for="(o,index) in common.el_with_keyword_menu.keyword_options"
			:key="index"
			@click="choose(o)"
		>
		  {{ o.keyword }}
		</v-list-item>
	</v-list>
</v-menu>

<v-dialog
  v-model="js_code_dialog"
  width="500"
>
	<v-card>
		<div class="dont_ignore_spaces code pa-2"> {{js_code}} </div>
	</v-card>
</v-dialog>


<v-navigation-drawer
	v-model="common.panel_drawer"
	absolute dark
	temporary
>
	<Panel/>
</v-navigation-drawer>

</div></template>



