function adapt(ast) {
	Remember.off();
	traverse(ast, {
		enter(path) {
			let P = path.node;

			delete P.start;
			delete P.end;
			delete P.loc;

			if (P.type == 'UpdateExpression' ) {
				let operator = {'++':'+=', '--':'-='}[P.operator];
				path.replaceWith( t.assignmentExpression( operator, P.argument, t.numericLiteral(1) ) );
			}
			else if (P.type == 'AssignmentExpression' && P.operator != '=' ) {
				let operator = P.operator[0];
				P.operator = '=';
				let left = {type: 'ItselfLiteral'};
				//let left = P.left;
				P.right = {
					operator,
					left,
					right: P.right,
					type: 'BinaryExpression'
				}
			}
			else if (P.type == 'IfStatement') {
				if (P.consequent.type != 'BlockStatement') P.consequent = t.blockStatement([ P.consequent ]);
				if (P.alternate && !['BlockStatement','IfStatement'].includes(P.alternate.type) ) P.alternate = t.blockStatement([ P.alternate ]);
			}
			else if (P.type == 'MemberExpression' && !P.computed) {
				P.computed = true;
				P.property = t.stringLiteral(P.property.name);
			}
			else if (P.type == 'ObjectExpression') {
				for (let k of P.properties) {
					if (k.key.type != 'StringLiteral') k.key = t.stringLiteral(k.key.name);
				}
			}
			else if (['ForStatement','ForOfStatement','WhileStatement'].includes(P.type)  ) {
				if (P.body.type != 'BlockStatement') P.body = t.blockStatement([ P.body ]);

				if (P.type == 'ForOfStatement' && P.left.declarations) {
					P.left = P.left.declarations[0].id;
				}
			}
			
			//console.log(path);
			if (path.listKey) {
				P._parent = path.parent[path.listKey];
			}
			else {
				P._parent = path.parent;
				P._keyAsChild = path.key;
			}

			P._main_parent = path.parent;

			//console.log(P);
			
		}
	});

	function before(P) {
		if ( P.type == 'ExpressionStatement' ) {
			replace_node(P, P.expression);
		}
		else if ( P.type == 'RestElement') {
			P.type = 'SpreadElement';
		}
		else if (P.type == 'ArrayPattern') {
			P.type = 'ArrayExpression';
		}
		else if (P.type == 'IfStatement') {
			let first_condition = {type: 'ConditionStatement', test: P.test, consequent: P.consequent};

			P.conditions = [first_condition];

			delete P.test;
			delete P.consequent;

			let M = P;
			while (M.alternate && M.alternate.type == 'IfStatement' ) {
				M = M.alternate;
				let c = {type: 'ConditionStatement', test: M.test, consequent: M.consequent};
				P.conditions.push(c);
			}

			if (M.alternate) {
				P.alternate = {type:'ElseStatement', body: M.alternate};
			}
			else P.alternate = null;
		}
		else if (P.type == 'Identifier') {
			P.value = P.name;
			delete P.name;
		}
	}

	function after(P) {
		if (P.type == 'ForStatement') {
			P.type = 'WhileStatement';
			if (P.init) {
				P._parent.splice( P._parent.indexOf(P), 0, P.init);
			}
			if (!P.test) P.test = {type: 'BooleanLiteral', value: true};
			if (P.update) {
				P.body.body.push(P.update);
			}
			
			delete P.init;
			delete P.update;
		}
		else if (P.type == 'UnaryExpression' && P.operator == '-' ) {
			if (P.argument.type == 'NumericLiteral') {
				replace_node(P, {
					type: 'NumericLiteral',
					value: '-' + P.argument.value,
				}, true);
			}
			else replace_node(P, {
				type: 'BinaryExpression',
				operator: '*',
				left: {type: 'NumericLiteral', value: '-1'},
				right: P.argument,
			}, true);
		}
		else if (P.type == 'FunctionDeclaration') {
			let F = clone(P);
			F.type = 'FunctionExpression';
			delete F.id;

			replace_node(P, {
				type: 'AssignmentExpression',
				operator: '=',
				left: P.id,
				right: F,
			}, true);
		}
		else if (P.type == 'VariableDeclaration') {
			let sets = [];
			for (let d of P.declarations) {
				sets.push({ type: 'AssignmentExpression', operator: '=', left: d.id, right: d.init || {type: 'Identifier', value: 'undefined'} });
			}

			P._parent.splice(P._parent.indexOf(P), 1, ...sets);
		}
	}

	tree_walk(ast, before, after);

	set_all_relationships(ast);
	Remember.on();
}

function to_normal_js(ast) {
	Remember.off();
	//console.log(ast);
	ast = clone(ast);

	tree_walk(ast, function(P){
		if (P.disabled) {	
			if (Array.isArray(P._parent)) {
				P._parent.splice( P._parent.indexOf(P), 1);
			}
			else {
				//replace_node(P, null);
				P._main_parent[P._keyAsChild] = null;
			}
		}
		else if (P.type == 'Separator' || P.type == 'Comment') {
			remove_piece(P);
		}
		else if (P.type == 'Empty' || P.type == 'Broken') {
			if (P._main_parent.type == 'BlockStatement') separate_child(P);
			else replace_node(P, {
				type: 'Identifier',
				value: 'undefined',
			});
		}
	});

	let scopes = [[]];
	let cur_scope = () => scopes[scopes.length-1];

	function log_stop(P) {
		return ['Program','BlockStatement','LogicalExpression','ArrayExpression','ObjectProperty','ConditionStatement' ].includes(P._main_parent.type) || must_be_a_line(P._main_parent);
		 // || (Array.isArray(P._parent._parent)  && P._parent._main_parent.type != 'CallExpression' ) ;
	}

	function before(P) {
		//console.log(P);
		if (P.type == 'ItselfLiteral') {
			let ancestor = P;
			while (ancestor.type != 'AssignmentExpression') {
				ancestor = ancestor._main_parent;
			}
			replace_node(P, ancestor.left );
		}
		else if (P.type == 'ObjectExpression' && P._keyAsChild == 'left' && P._main_parent.type == 'AssignmentExpression') {
			P.type = 'ObjectPattern';
		}
		else if (P.type == 'FunctionExpression' || P.type == 'FunctionDeclaration') {
			let new_scope = P.params.filter(x => x.type == 'Identifier').map(x => x.value);
			scopes.push(new_scope);
		}
		else if (P.type == 'Identifier') {
			if (!(P._parent.type == 'AssignmentExpression' &&  P._keyAsChild == 'left')) cur_scope().push(P.value);
			P.name = P.value;
		}
		else if (P.type == 'PrintStatement') {
			replace_node(P, {
				type: 'CallExpression',
				callee: {
					type: 'MemberExpression',
					object: {type:'Identifier','name':'console'},
					property: {type:'Identifier', 'name':'log'},
				},
				arguments: [P.argument]
			}, true);
		}
		else if (P.type == 'RootExpression') {
			replace_node(P, {
				type: 'CallExpression',
				callee: {
					type: 'MemberExpression',
					object: {type:'Identifier', 'name':'Math'},
					property: {type:'Identifier', 'name':'sqrt'},
				},
				arguments: [P.argument]
			}, true);
		}
		else if (P.type == 'ForOfStatement') {
			replace_node(P.left, {
				type: 'VariableDeclaration',
				kind: 'let',
				declarations: [{
					type: 'VariableDeclarator',
					id: clone(P.left),
					//init: P.right,
				}]
			}, true);
		}
	}

	function after(P) {
		if (P.type == 'FunctionExpression' || P.type == 'FunctionDeclaration') {
			scopes.pop();
		}
		else if (P.type == 'GlobalVariableStatement') {
			P._parent.splice( P._parent.indexOf(P), 1);
		}
		else if (P.type == 'AssignmentExpression' && P.operator == '=' && P.left.type == 'Identifier'
			&& !cur_scope().includes(P.left.name)  ) {
			replace_node(P, {
				type: 'VariableDeclaration',
				kind: 'var',
				declarations: [{
					type: 'VariableDeclarator',
					id: P.left,
					init: P.right,
				}]
			}, true);
			cur_scope().push(P.left.name);
		}
		else if (P.type == 'IfStatement') {
			let A = P.conditions;
			if (!A) return;
			P.test = A[0].test;
			P.consequent = A[0].consequent;
			let final_else = P.alternate;
			let prev = P;
			let s = P;
			
			for (let i=1; i < A.length; i++) {
				s = {type: 'IfStatement', test: A[i].test, consequent: A[i].consequent };
				prev.alternate = s;
				prev = s;
			}

			if (final_else) s.alternate = final_else.body;
			//delete P.alternate;
			set_all_relationships(P);
		}
		else if (P.type == 'LogicalExpression' || P.type == 'UnaryExpression'  ) {
			let ancestor = P;
			while (  ! log_stop(ancestor) ) {
				ancestor = ancestor._main_parent;
				//console.log({ancestor});
			}

			if (ancestor != P) {
				//console.log("Gotta factor in!", ancestor);
				
				let special_replace_node;
				let i;
				if ( Array.isArray(P._parent) ) i = P._parent.indexOf(P);
				else i = P._keyAsChild;

				tree_walk(P, function(leaf){
					if (! ['LogicalExpression','UnaryExpression'].includes(leaf.type) ) {
						P._parent[ i ] = leaf;

						let C = clone(ancestor);
						replace_node(leaf, C);
					}
				});

				replace_node(ancestor, P, true);
			}
		}
		else if (P.type == 'ForRangeStatement') {
			P.init = {
				type: 'VariableDeclaration',
				kind: 'let',
				declarations: [{
					type: 'VariableDeclarator',
					id: P.var,
					init: P.first,
				}]
			};
			
			P.update = {
				type: 'AssignmentExpression',
				operator: '+=',
				left: P.var,
				right: P.step || {type: 'NumericLiteral', value: 1},
			};

			P.test = {
				type: 'BinaryExpression',
				operator: (P.step && P.step.type == 'UnaryExpression' ) ? '>=' : '<=',
				left: P.var,
				right: P.last,
			};

			delete P.first;
			delete P.last;
			delete P.var;

			P.type = 'ForStatement';
		}
		else if (P.type == 'NumericLiteral' && P.value[0] == '-' ) {
			P.value = P.value.slice(1);
			replace_node(P, {
				type: 'UnaryExpression',
				operator: '-',
				argument: clone(P)
			}, true);
		}
		else if (P.type == 'MemberExpression' && P.property.type == 'StringLiteral') {
			let k = P.property.value;
			if (k.match(/^[A-Za-z$_][A-Za-z$_0-9]*$/) !== null) {
				P.computed = false;
				P.property = {type: 'Identifier', name: k };
				set_all_relationships(P);
			}

		}
		else if (P._main_parent && ['BlockStatement','Program'].includes(P._main_parent.type)
			&& !['IfStatement', 'WhileStatement', 'ForOfStatement', 'BreakStatement', 'ContinueStatement', 'ReturnStatement', 'TryStatement'].includes(P.type) ) {
			replace_node(P, {
				type: 'ExpressionStatement',
				expression: P,
			})
		}
	}

	tree_walk(ast, before, after);

	Remember.on();
	return ast;
}

