common.toggle_disabled = function() {
	for (let P of $g.selected_pieces) {
		if (parent_is_array(P) || P.type == 'ElseStatement' ) register_child(P, 'disabled', !P.disabled);
	}
	unselect_all();
}

let ast = parse(test);
adapt(ast);
//console.log(ast);

function downloadFiles(data, file_name, file_type) {
    var file = new Blob([data], {type: file_type});
    if (window.navigator.msSaveOrOpenBlob) 
        window.navigator.msSaveOrOpenBlob(file, file_name);
    else { 
        var a = document.createElement("a"),
                url = URL.createObjectURL(file);
        a.href = url;
        a.download = file_name;
        document.body.appendChild(a);
        a.click();
        setTimeout(function() {
            document.body.removeChild(a);
            window.URL.revokeObjectURL(url);  
        }, 0); 
    }
}



let gonna_compile;

import Panel from '@/components/Panel'

export default {
	components: {Panel},
	data: () => ({
		ast,
		js_code: '',
		js_code_dialog: false,
		cur_tab: 0,
		menu,
		common,
		$g,
		console: [],
	}),
	watch: {
		console() {
			this.$refs.console.scrollTo(0,this.$refs.console.scrollHeight);
		},
		/*
		ast() {
			clearTimeout(gonna_compile);
			gonna_compile = setTimeout(() => {
				this.js_code = babel_generate(this.ast).code;
			}, 600);
		}*/
		/*
		'common.focused': function(nv) {
			if (common.focused)
				common.cur_block = find_ancestor(common.focused, P => P.type == 'BlockStatement');
			else common.cur_block = null;
		},*/
		'$data.$g.selected_pieces': function() {
			if ($g.selected_pieces.length == 0) {
				common.paste_mode = null;
				common.select_many = false;
			}
			else if (common.panel_drawer && !common.select_many) {
				common.panel_drawer = false;
			}
		},
		'common.show_keyword_menu': function(nv) {
			//console.log('common.show_keyword_menu = ', nv);
		},
		/*
		'common.hover_P': function() {
			$g.console.log("hover changed!");
			if (common.hover_P == null && common.glow_side_P) {
				common.glow_side_P._vm.glow = {left: false, right: false};
			}
		}*/
	},
	methods: {
		choose(o) {
			clearTimeout(common.timeout_to_choose_first);
			common.el_with_keyword_menu.transform_from_keyword(o.piece_type);
		},
		generate() {
			this.js_code = babel_generate( to_normal_js(this.ast) ).code.trim();
		},
		run() {
			this.console = [];
			this.generate();
			run_code(this.js_code);
		},
		undo() { Remember.undo(); },
		redo() { Remember.redo(); },
		set_mode(mode) {
			if (common.paste_mode == null) common.paste_mode= mode;
			else common.paste_mode = null;
		},
		remove() {
			for (let p of $g.selected_pieces) {
				remove_piece(p);
			}
			unselect_all();
		},
		select_many() {
			common.select_many = !common.select_many;
		},
		save() {
		    downloadFiles(JSON.stringify(clone(this.ast, true)), 'code.vcc', 'vcc');
		},
		handle_file_input(event) {
			var reader = new FileReader();
			reader.onload = (e) => {
				var data = e.target.result;
				//console.log(data);
				let ast = JSON.parse(data);
				set_all_relationships(ast);
				this.ast = ast;
			};
			reader.readAsText(event.target.files[0]);
		},
		sort_by_order(pieces) {
			let res = pieces.filter(p => p.is_sample);
			tree_walk(this.ast, function(P){
				if (pieces.includes(P)) {
					res.push(P);
				}
			});
			return res;
		},
	},
	mounted() {

	},
	computed: {
		menu_items() {
			if (!this.menu.under_menu) return;
			let P = this.menu.under_menu.P;
			let items = ['Remove','Remove parent','Remove operator'];
			if (parent_is_array(P) ) items.push(P.disabled ? 'Enable':'Disable');
			return items;
		},
		/*
		cur_block() {
			let el = common.focused || ($g.selected_pieces.length == 1 && $g.selected_pieces[0]);
		
			return el ? find_ancestor(el, P => P.type == 'BlockStatement') : null;
		}*/
		persistent_panel() {
			return this.$vuetify.breakpoint.smAndUp;
		}
	},
	created() {
		common.IDE = this;

		$g.console = console;
		console = {
			log(...args) {
				let console_code = args.map( function(a){
					if (typeof a == 'object') return '(' + JSON.stringify(a) + ')';
					else if (typeof a == 'string') return '("' + a.replace(/"/g, '\\"').replace(/\n/g, '\\n') + '")';
					else return a;
				}).join(';') ;

				//$g.console.log({console_code});
				ast = parse(console_code);
				adapt(ast);

				common.IDE.console.push({
					type: 'consolelog',
					ast,
				});
				$g.console.log(...args);
			},
			error(...args) {
				$g.console.error(...args);
				
				for (let a of args) {
					if (a.match && a.match(/\[Vue warn\]/)) continue;
					common.IDE.console.push({type: 'consoleerror', content:a});
				}
			},
			warn(...args) {
				$g.console.warn(...args);

				for (let a of args) {
					if (a.match && a.match(/\[Vue warn\]/)) continue;
					common.IDE.console.push({type: 'consolewarn', content:a});
				}
			},
		};
	}
}
</script>

