import Vue from 'vue'

let history, action_bundle, shift_to_past, T, timeout;

let undo_after_finishing;

function record(R) {
	if (!active) return;

	if (shift_to_past > 0) {
		history.splice(history.length - shift_to_past, shift_to_past);
		shift_to_past = 0;
	}

	if (action_bundle.length == 0) {
		timeout = setTimeout(action_end, 100);
	}

	action_bundle.push(R);
}

function finish_action() {
	clearTimeout(timeout);
	if (action_bundle.length > 0) action_end();
}

function set(object, key, new_value, old_value) {
	if (old_value === undefined) old_value = object[key];
	if (old_value == new_value) return;
	
	record({
		action: 'set',
		object, key, new_value, old_value
	});

	Vue.set(object, key, new_value);
}

function remove(array, index) {
	record({
		action: 'remove',
		array, index,
		old_value: array[index],
	});
	array.splice(index, 1);
}

function add(array, index, new_value) {
	record({
		action: 'add',
		array, index, new_value
	});
	array.splice(index, 0, new_value);
}

function action_end() {
	//console.log(action_bundle);
	history.push(action_bundle);
	action_bundle = [];
	if (undo_after_finishing) {
		undo_after_finishing = false;
		undo();
	}
}

function reverse_action(R) {
	if (R.action == 'remove') {
		R.array.splice(R.index, 0, R.old_value);
	}
	else if (R.action == 'add') {
		R.array.splice(R.index, 1);
	}
	else if (R.action == 'set') {
		Vue.set(R.object, R.key, R.old_value);
	}
}

function play_action(R) {
	if (R.action == 'remove') {
		R.array.splice(R.index, 1);
	}
	else if (R.action == 'add') {
		R.array.splice(R.index, 0, R.new_value);
	}
	else if (R.action == 'set') {
		Vue.set(R.object, R.key, R.new_value);
	}
}

function is_in_the_middle_of_action() {
	return action_bundle.length > 0;
}

function undo() {
	if (is_in_the_middle_of_action() ) {
		undo_after_finishing = true;
		return;
	}

	let bundle = history[history.length - 1 - shift_to_past];
	//console.log(bundle);
	if (!bundle) return;

	for (let i = bundle.length-1; i >=0; i--) {
		reverse_action(bundle[i]);
	}

	shift_to_past++;
}

function redo() {
	if (is_in_the_middle_of_action() ) return;

	let bundle = history[history.length - shift_to_past];

	if (!bundle) return;

	for (let R of bundle) {
		play_action(R);
	}

	shift_to_past--;
}


function clear_history() {
	history = [];
	action_bundle = [];
	shift_to_past = 0;
}

clear_history();

let active = true;

function on() {
	active = true;
}

function off() {
	active = false;
}


export default {set, add, remove, action_end, undo, redo, clear_history, on, off, finish_action};