<template>
	<div
		tag="div" 
		spellcheck="false"
		lang="klingon"
		:contenteditable="true"
		:placeholder="placeholder"
		@focus="redirect"
		@blur="redirect"
		@keydown="handle_keydown"
		@keyup="handle_keyup"
		@input="handle_type"
		ref="CE"
	/>
</template>

<script>

import Vue from 'vue'

let before_backspace;

export default {
	props: {
		P: Object,
		placeholder: String,
	},
	data: () => ({
		edited_from_contenteditable: false
	}),
	watch: {
		'P.value': function() {
			this.on_change_value();
		},
	},
	methods: {
		redirect(event) {
			this.$emit(event.type, event);
		},
		handle_type(event) {
			if (this.$refs.CE.innerHTML == '<br>' || this.$refs.CE.innerHTML == '<div><br></div>') 
				this.$refs.CE.innerHTML = '';

			let innerHTML = this.$refs.CE.innerHTML.replace(/<div>(.*?)<\/div>/g, '$1\n');

			Vue.set(this.P, 'value', innerHTML.replace(/<br>/g, '').replace(/\n$/, '') );
			this.edited_from_contenteditable = true;

			//this.P.value = this.P.value.replace(/\\n\\n/g, '\\n');

			this.$emit(event.type, event);
		},
		handle_keydown(event) {
			var sel = window.getSelection();

			if (event.key == 'Backspace') {
				before_backspace = this.P.value;
			}
			else if (event.code == 'ArrowRight' && sel.anchorOffset == this.P.value.length
				|| event.code == 'ArrowLeft' && sel.anchorOffset == 0) {
				this.$emit('arrowexcess');
			}
			else if (sel.anchorOffset == this.P.value.length-1) setTimeout(() => {
				 this.setCaret(sel.anchorOffset);
			}, 50);


			this.$emit(event.type, event);

		},
		handle_keyup(event) {
			//console.log(window.getSelection() );
			//console.log(this.P.value);
			if (event.key == 'Backspace') {
				if (this.P.value == before_backspace) {
					this.$emit('backspaceAtBeginning');
				}
			}
		},
		on_change_value() {
			if (this.edited_from_contenteditable) {
				this.edited_from_contenteditable = false;
				return;
			}
			this.$refs.CE.innerHTML = this.P.value;
		},
		setCaret(pos) {
			const node = this.$refs.CE;
			node.focus();
			const textNode = node.firstChild;
			const range = document.createRange();
			range.setStart(textNode, pos);
			range.setEnd(textNode, pos);
			const sel = window.getSelection();
			sel.removeAllRanges();
			sel.addRange(range);
		},
		focus() {
			this.$refs.CE.focus();

			if (this.P.focus && this.P.focus.position !== undefined && this.P.value.length > 0) {
				let pos;
				if (this.P.focus.position == 'end') pos = this.P.value.length;
				else pos = this.P.focus.position;

				this.setCaret(pos);
			}
		}
	},
	mounted: function() {
		this.$nextTick(() => {
			this.on_change_value();
			if (this.P.focus) this.focus();
		});
	}
}
</script>
