<template><div
	:class="{hover: mark, brackets, disabled: P.disabled && !P.selected, piece: true,
		selected: P.selected,
		glow_right: glow.right,
		glow_left:  glow.left
	}"
	@mouseover="handle_mouseover"
	@mouseleave="handle_mouseleave"
	@click.right="open_menu"
	@click="handle_click"
>

<div v-if="show_blue('vertical')" class="blue_line" @mouseover="handle_blue_mouseover" @mouseleave="handle_blue_mouseleave" @click="handle_blue_click('top')"><div></div></div>
<div v-if="show_blue('vertical')" class="blue_line blue_line_bottom" @mouseover="handle_blue_mouseover" @mouseleave="handle_blue_mouseleave" @click="handle_blue_click('bottom')"><div></div></div>

<div v-if="['Empty','Identifier','NumericLiteral','Broken'].includes(P.type) ">
	<div :class="P.type">
		<autosize-input
			v-if="P._parent"
			@focus="handle_input_focus"
			@blur="handle_input_blur"
			@change="handle_change"
			@keydown="handle_keydown"
			class="autosize-input"	
			v-model="P.value"
			:placeholder="placeholder || '      '"
			ref="input_field"
		/>
		<span v-else-if="!P._parent" > {{ P.value }} </span>
	</div>
</div>


<div
	v-else-if=" ['Program', 'BlockStatement'].includes(P.type)"
	:class="{block: P.type == 'BlockStatement', cur_block, block_margin: P.type == 'BlockStatement' && P._main_parent && P._main_parent.type != 'ConditionStatement' }"
>
	<div v-if="show_minimize_btn" class="minimize_btn" @mousedown.stop="toggle_minimize_block" @click.stop> {{ P.minimized ? '+' : '-' }} </div>
	
	<div v-if="P.body.length == 0" class="Empty slide-item" > </div>


	<transition-group v-if="!P.minimized" name="list-complete" tag="div" key="block" >
	<div v-for="i in P.body.length" class="list-complete-item" :key="get_id(P.body[i-1])">
		<div class="line_with_anchor">
			<div
				v-if="show_anchor(i)"
				class="line_anchor"
				@mousedown.stop="handle_anchor(i-1, $event)"
				@mouseover.stop.prevent
			>  </div>
			<Piece v-if="P.body[i-1]" :P="P.body[i-1]"/>
		</div>
	</div>
	</transition-group>

	<div v-else class="dotdotdot-item" > ... </div>
</div>

<div v-else-if="P.type == 'ExpressionStatement' ">
	~
	<Piece :P="P.expression"/>
</div>

<div v-else-if="P.type == 'CallExpression' || P.type == 'NewExpression' " class="d-flex align-center">
	<div v-if="P.type == 'NewExpression' "> new </div>
	<Piece :P="P.callee"/>
	<span class="exclamation_mark"> ! </span>
	<button v-if="!P.is_sample && hover && P.arguments.length == 0" class="btn_add_property" @click.stop="add_new_argument"> + </button>

	<div v-if="!P.is_sample">

		<transition-group name="list-complete" tag="div" >
		<div v-for="i in P.arguments.length+1" class="d-flex align-stretch list-complete-item" :key="get_id(P.arguments[i-1])">
			<div class="d-flex align-center">
				<div v-if=" P.arguments.length > 1 && P.arguments[i-1]" :class="{empty_line: true, 'align-end': i == 1}">
					<div :class="i == 1 || i == P.arguments.length ? 'half_height' : 'full_height' "/>
				</div>
				<div v-if="P.arguments.length > 1" :class="{'hor_con ml-0':true, squeeze: !P.arguments[i-1] }"/>
				<div class="d-flex line_with_anchor">
					<div
						v-if="show_anchor(i)"
						class="line_anchor"
						@mouseover.stop.prevent
						@mousedown.stop="handle_anchor(i-1, $event)"
					>  </div>
					<Piece :need_brackets="P.arguments.length == 1" v-if="P.arguments[i-1]" :P="P.arguments[i-1]"/>
				</div>
			</div>
		</div>
		</transition-group>
	</div>
</div>

<div v-else-if="P.type == 'UnaryExpression' " class="d-flex align-center">
	<span class="pl-1"> {{ edit_operator(P.operator) }} </span>
	<Piece :vertical_view="vertical_view" :need_brackets="maybe_brackets_for_term(P, 'argument')" :P="P.argument"/>
</div>

<div v-else-if="P.type == 'MemberExpression'" class="d-flex align-center">
	<Piece :need_brackets="!is_primitive(P.object) && P.object.type != 'MemberExpression' " :P="P.object"/>
	<v-icon color="white" class="member_arrow"> {{ $mdi.MenuRight }} </v-icon>
	<Piece :need_brackets="!is_primitive(P.property) " :P="P.property"/>
</div>


<div v-else-if="P.type == 'ObjectExpression' && gotta_show_full" class="Unit align-center">
	<div class="ObjectExpressionCap d-flex px-2 justify-center">
		{{ P.properties.length > 0 || P.is_sample ? 'Object' : 'Empty object' }}
		<button v-if="!P.is_sample && hover" class="btn_add_property ml-1" @click.stop="add_new_property"> + </button>
	</div>

	<transition-group name="list-complete" tag="div">
		<div v-for="k in P.properties" class="d-flex flex-column list-complete-item" :key="get_id(k)">
			<Piece :P="k"  />
		</div>
	</transition-group>
</div>


<div v-else-if="P.type == 'ObjectProperty' " class="d-flex">
	<div class="nowrap">
		<Piece :P="P.key"/> : 
	</div>
	<Piece :P="P.value"/>
</div>


<div class="Unit" v-else-if="P.type == 'ArrayExpression' && gotta_show_full" >
	<div class="ArrayExpressionCap d-flex px-2 justify-center">
		{{ P.elements.length > 0 || P.is_sample ? piece_name : 'Empty ' + piece_name }}
		<button v-if="!P.is_sample && hover" class="btn_add_property ml-1" @click.stop="add_new_element"> + </button>
	</div>
	<div v-if="P.elements.length > 0">

		<transition-group name="list-complete" tag="div" >
		<div v-for="i in P.elements.length+1" class="d-flex list-complete-item" :key="get_id(P.elements[i-1])" >
			<div :class="{'pt-1': true, squeeze: !P.elements[i-1]}">
				<span class="pl-2 nowrap">{{ smart_index(i-1) }} : </span> 
			</div>
			<div class="line_with_anchor">
				<div
					v-if="show_anchor(i)"
					class="line_anchor"
					@mouseover.stop.prevent
					@mousedown.stop="handle_anchor(i-1, $event)"
				>  </div>
				<Piece v-if="P.elements[i-1]" :P="P.elements[i-1]"/>
			</div>
		</div>
		</transition-group>

	</div>

</div>

<div v-else-if="(P.type == 'ObjectExpression' || P.type == 'ArrayExpression') && is_child_in_json" class="d-flex align-center">
	<v-icon small color="white" @mousedown.stop="folder_click" @click.stop class="path_bit"> {{ $mdi.FolderOpen }} </v-icon>
	<div class="Unit align-center">
		<div :class="P.type + 'Cap d-flex px-2 justify-center align-center' ">
			{{ piece_name + '...' }}
		</div>
	</div>
</div>

<div v-else-if="P.type == 'ObjectExpression' || P.type == 'ArrayExpression' " class="Unit align-center">
	<div v-if="P.type == 'ObjectExpression' " class="ObjectExpressionCap d-flex px-2 justify-center">
		{{ P.properties.length > 0 || P.is_sample ? 'Object' : 'Empty object' }}
	</div>
	<div v-else class="ArrayExpressionCap d-flex px-2 justify-center">
		{{ P.elements.length > 0 || P.is_sample ? 'List' : 'Empty List' }}
	</div>

	<div class="d-flex align-center">
		<v-icon color="white" @mousedown.stop="cut_path(0, $event)" @click.stop class="path_bit"> {{ $mdi.SemanticWeb }} </v-icon>
		<div v-for="(k,index) in json_path" @mousedown.stop="cut_path(index+1, $event)" @click.stop class="d-flex align-center path_bit">
			<v-icon color="white" > {{ $mdi.MenuRight }} </v-icon>
			<span> {{ k }} </span>
		</div>
	</div>

	<Piece v-if="P" :P="cur_folder" show_full  />
</div>


<div v-else-if="P.type == 'AssignmentExpression' && P.operator == '=' && P.right.type == 'FunctionExpression' ">
	<div class="d-flex align-center">
		Set
		<Piece v-if="!P.is_sample" :P="P.left"/>
		<div v-else class="Identifier"> go </div>
		<span class="mr-1"> 
		<span v-if="P.right.async"> Async </span>
		Function </span>
		<div class="d-flex align-center" v-if="!P.is_sample">
			<span v-if="P.right.params.length"> with input </span>
			<Piece v-for="a in P.right.params" :P="a"/>
			<button class="btn_add_property my-2 mx-1" @click.stop="add_new_param"> + </button>
		</div>
	</div>

	<Piece :P="P.right.body"/>
</div>

<div v-else-if="P.type == 'AssignmentExpression' && P.operator == '=' " class="d-flex align-center">
	<span>Set</span>
	<Piece :P="P.left"/>
	
	<Piece :P="P.right"/>
</div>

<div v-else-if="P.type == 'BinaryExpression' && P.operator == '/' " class="d-flex flex-column align-center px-1">
	<Piece :P="P.left"/>
	<div class="division_line"> </div>
	<Piece :P="P.right"/>
</div>

<div v-else-if="P.type == 'xBinaryExpression' && P.operator == '**' " class="d-flex align-end">
	<Piece :P="P.left"/>
	<Piece style="transform-origin: 0% -30%; transform: scale(0.7);" :P="P.right"/>
</div>

<div v-else-if="P.type == 'BinaryExpression' && P.operator == '%' " class="d-flex align-center">
	<span class="pl-1"> Remainder of </span>
	<Piece :need_brackets="!is_primitive(P.left)" :P="P.left"/>
	<div class="d-flex justify-center align-center px-2" style="height: 10px; width: 5px;" >
	
	</div>
	<Piece :need_brackets="!is_primitive(P.right)" :P="P.right"/>
</div>

<div v-else-if="P.type == 'BinaryExpression' " class="d-flex align-center">
	<Piece :need_brackets="maybe_brackets_for_term(P, 'left')" :P="P.left"/>
	<span v-if="P.is_sample"> {{ edit_operator(P.operator) }} </span>
	<div v-else class="d-flex justify-center align-center px-2" style="height: 10px; width: 5px;" >
	{{ edit_operator(P.operator) }}
	</div>
	<Piece :need_brackets="maybe_brackets_for_term(P, 'right')" :P="P.right"/>
</div>






<div v-else-if="P.type == 'RootExpression' " class="RootExpression d-flex">
	<div style="width: 15px"/>
	<svg style="width: 15px; position: absolute; height: 100% " ref="root_svg">
		<line x1="20%" y1="70%" x2="50%" y2="100%" style="stroke: white; stroke-width: 2" />
		<line x1="50%" y1="100%" x2="100%" y2="0" style="stroke: white; stroke-width: 2" />
	</svg>

	<div class="under_root">
		<Piece :P="P.argument"/>
	</div>
</div>





<div v-else-if="P.type == 'LogicalExpression' " :class="{'d-flex align-center':true, vertical_view}">
	<Piece :vertical_view="get_view(P.left)" :need_brackets="bracket_vertical(P.left)" :P="P.left"/>

	<span v-if="P.is_sample"> {{ edit_operator(P.operator) }} </span>
	<div v-else class="d-flex justify-center align-center px-2" >
	{{ edit_operator(P.operator) }}
	</div>

	<Piece :vertical_view="get_view(P.right)" :need_brackets="bracket_vertical(P.right)" :P="P.right"/>
</div>

<div v-else-if="P.type == 'GlobalVariableStatement' ">
	Use global
	<Piece v-if="!P._parent" :P="{type: 'Identifier', value: 'x'}" />
	<Piece v-else :P="P.var" />
</div>

<div v-else-if="P.type == 'StringLiteral' " class="String align-center d-flex">
	<div class="quotes"></div>

	<span v-if="!P._parent">{{ P.value || 'Hello' }}</span>

	<contenteditable
		v-else
		:P="P"
		placeholder="empty string"
		ref="CE"
		@keydown="handle_keydown_CE"
		@focus="handle_input_focus"
		@blur="handle_input_blur"
		@input="unselect_all()"
		@backspaceAtBeginning="handle_backspace_at_beginning"
		@arrowexcess="handle_arrow_excess"
	/>

	<div class="quotes"></div>
</div>

<div v-else-if="P.type == 'BooleanLiteral' " class="NumericLiteral">
	<span> {{ edit_operator(P.value) }} </span>
</div>

<div v-else-if="P.type == 'NullLiteral' " class="Null">
	no value
</div>

<div v-else-if="P.type == 'ItselfLiteral' " class="Null">
	itself
</div>

<div v-else-if="P.type == 'IfStatement' " class="block_margin">
	<div v-for="(c,index) in P.conditions">
		<Piece :index_as_child="index"  :P="c"/>
	</div>

	<v-menu offset-y>
      <template v-slot:activator="{ on, attrs }">
      	<button v-if="!P.is_sample" v-bind="attrs" v-on="on" class="btn_add_property mb-1"> + </button>
      </template>
      <v-list dense>
        <v-list-item
          v-for="(item,index) in elseif_else_arr"
          :key="index"
          @click="add_new_condition(index)"
        >
          {{ item }}
        </v-list-item>
      </v-list>
    </v-menu>

	<div> <Piece v-if="P.alternate" :P="P.alternate"/> </div>
</div>

<div v-else-if="P.type == 'ConditionStatement' " >
	<div class="d-flex align-center">
		<span v-if="index_as_child != 0" class="pr-1"> else </span>
		<span> if </span>
		<Piece :P="P.test"/> then 
	</div>
	<Piece :P="P.consequent"/>
</div>

<div v-else-if="P.type == 'ElseStatement' " >
	<div>if none of the above then</div>
	<Piece :P="P.body"/>
</div>

<div v-else-if="P.type == 'ForOfStatement' " >
	<div v-if="P.is_sample"> Repeat... (array) </div>
	<div v-else class="d-flex align-center">
		Repeat for every
		<Piece :P="P.left"/> 
		in
		<Piece :P="P.right"/>
		
	</div>
	<div>
		<Piece :P="P.body"/>
	</div>
</div>

<div v-else-if="P.type == 'ForRangeStatement' " >
	<div v-if="P.is_sample"> Repeat...(index) </div>
	<div v-else class="d-flex align-center">
		Repeat for
		<Piece :P="P.var"/> 
		from
		<Piece :P="P.first"/>
		to
		<Piece :P="P.last"/>
		<span v-if="P.step">
			with step
			<Piece :P="P.step"/>
		</span>
		<button v-if="!P.step" class="btn_add_property mb-1" @mousedown.stop="add_step"> + </button>
		<button v-else class="btn_add_property mb-1" @mousedown.stop="remove_step"> - </button>
		:
	</div>
	<div>
	<Piece :P="P.body"/>
	</div>
</div>

<div v-else-if="P.type == 'WhileStatement' " >
	<div class="d-flex align-center">
		Repeat while
		<Piece :P="P.test"/>
	</div>
	<div>
	<Piece :P="P.body"/>
	</div>
</div>

<div v-else-if="P.type == 'FunctionExpression' && P.is_sample">
	<div>Function</div>
	<Piece :P="P.body"/>
</div>

<div v-else-if="P.type == 'Async' " class="d-flex align-center"> 
	add/remove async
</div>

<div v-else-if="P.type == 'ThisExpression' " class="d-flex align-center"> 
	<v-icon color="white" > {{ $mdi.SemanticWeb }} </v-icon>
</div>

<div v-else-if="P.type == 'FunctionExpression' ">
	<div class="d-flex align-center">
		<span class="mr-1">
		<span v-if="P.async"> Async </span>
		Function
		</span>
		<span v-if="P.params.length"> with input </span>
		<Piece v-for="a in P.params" :P="a"/>
		<button v-if="!P.is_sample" class="btn_add_property my-2 mx-1" @mousedown.stop="add_new_param"> + </button>
	</div>

	<Piece :P="P.body"/>
</div>

<div v-else-if="P.type == 'ObjectMethod' ">
	<div class="d-flex align-center">
		<span class="mx-1">
		<span v-if="P.async"> Async </span>
		{{ P.kind }}
		</span>
		<Piece :P="P.key"> </Piece>
		<span v-if="P.params.length"> with input </span>
		<Piece v-for="a in P.params" :P="a"/>
		<button v-if="!P.is_sample && P.kind != 'get' " class="btn_add_property my-2 mx-1" @mousedown.stop="add_new_param"> + </button>
	</div>

	<Piece :P="P.body"/>
</div>


<div v-else-if="P.type == 'ReturnStatement' && P.is_sample">
	Exit function / output
</div>

<div v-else-if="P.type == 'ReturnStatement' " class="d-flex align-center">
	Exit and output
	<Piece placeholder="nothing" :P="P.argument"/>
</div>

<div v-else-if="P.type == 'ContinueStatement' ">
	Next loop iteration
</div>

<div v-else-if="P.type == 'BreakStatement' ">
	Exit the loop
</div>

<div v-else-if="P.type == 'PrintStatement' " class="d-flex align-center">
	Print
	<Piece :P="P.argument"/>
</div>

<div v-else-if="P.type == 'Separator' && P.is_sample " class="py-1">
	Separator
</div>

<div v-else-if="P.type == 'Separator' " class="Separator posrel" :style="'height:' + P.height + 'px' ">
	<v-slider
	  v-if="hover"
	  @click.stop=""
	  dense
	  hide-details
	  max="100"
	  min="30"
	  v-model="P.height"
	></v-slider>
</div>

<div v-else-if="P.type == 'Comment' " class="Comment d-flex align-center justify-center">
	<v-icon class="pr-2" color="blue"> {{ $mdi.MessageProcessing }} </v-icon>
	<span v-if="P.is_sample"> Comment </span>
	<contenteditable
		v-else
		:P="P"
		ref="CE"
		@keydown="handle_keydown_CE"
		placeholder="Write your comment..."
		@focus="handle_input_focus"
		@blur="handle_input_blur"
		@input="unselect_all()"
		@backspaceAtBeginning="handle_backspace_at_beginning"
		@arrowexcess="handle_arrow_excess"
	/>
</div>

<div v-else-if="P.type == 'AwaitExpression' " class="d-flex align-center justify-center">
	await
	<Piece :P="P.argument"/>
</div>

<div v-else-if="P.type == 'TryStatement' " class="mb-5" >
	<div> Try </div>
	<Piece :P="P.block" style="margin-bottom: -20px"  />
	<div v-if="!P.is_sample && P.handler">
		<div> If an error ( <Piece :P="P.handler.param" /> ) happened, then </div>
		<Piece :P="P.handler.body" style="margin-bottom: -20px" />
	</div>

	<div v-if="P.finalizer">
		<div> Run in any case (no matter if an error happened) </div>
		<Piece :P="P.finalizer" style="margin-bottom: -20px"/>
	</div>

	<button v-if="!P.is_sample && hover" class="btn_add_property mb-1" style="position: absolute" @click.stop="toggle_finally"> {{ P.finalizer ? '-':'+' }} </button>

</div>

<div v-else-if="P.type == 'SpreadElement' " :class="{'d-flex flex-column': P._main_parent.type != 'FunctionExpression' }">
	<Piece :P="{type: 'MemberExpression', object: P.argument, property: {type:'NumericLiteral', value:0} }" />
	<Piece :P="{type: 'MemberExpression', object: P.argument, property: {type:'NumericLiteral', value:1} }" />
	etc...
</div>


<div v-else>
	???? what the fuck is {{ P.type }} ????
</div>


</div></template>


<script>


import Remember from '@/remember'

import {$g, common, separate_child, remove_piece, register_child, register_child_in_arr, replace_node, clone, set_all_relationships, menu, must_be_a_line, isObject, parent_is_array, find_ancestor, unselect_all } from '@/methods'
import samples from '@/samples.js'


function get_sample(piece_type) {
	let S = clone( samples[piece_type] );
	return S;
}

let keywords = {
	'ForOfStatement': ['for (array)', 'repeat (array)'],
	'ForRangeStatement': ['for (index)', 'repeat (index)'],
	'While': ['repeat while'],
	'Array': ['list'],
	'Itself': ['self'],
	'Break': ['exit loop'],
	'Return': ['exit function', 'output'],
	'Continue': ['next loop iteration'],
	'Mod': ['remainder', '%'],
	'^': ['**', 'power'],
	'!=': ['NotEquals'],
	'Increment': ['+=', 'add number to itself'],
	'IncrementBy1': ['++', 'add 1 to itself'],
	'Push': ['add element to array'],
	'Length': ['size (list/string)'],
};

let remove_keywords = ['Else', 'ForRangeStatement', 'ForOfStatement', 'ObjectProperty', 'ConditionStatement', 'Async', 'Empty', 'panel_samples'];
let available_samples = Object.keys(samples).filter(s => !remove_keywords.includes(s) );

function find_by_keyword(s) {
	s = s.toLowerCase();
	let res = [];
	for (let piece_type of available_samples) {
		if (piece_type.toLowerCase().startsWith(s) ) res.push({piece_type, keyword: piece_type, default: keywords[piece_type] ? keywords[piece_type][0] : piece_type });
	}

	for (let piece_type in keywords) {
		for (let keyword of keywords[piece_type] ) {
			if (keyword.startsWith(s) ) res.push({piece_type, keyword, default: keywords[piece_type][0] });
		}
	}

	return res;
}

function get_children(P) {
	let res = [];

	for (let k in P) {
		if (k[0] != '_' && typeof P[k] == 'object' && P[k].type) res.push(P[k]);
	}
	return res;
}

function remove_operator(P) {
	let children = get_children(P).filter(C => C.type != 'Empty');

	if (children.length == 1 && compatible(P._main_parent, children[0], P._keyAsChild  ) ) {
		replace_node( P, children[0] );
		return children[0];
	}
	else if (children.length == 0) {
		let E = {type: 'Empty', focus: {} };
		replace_node( P, E );
		return E;
	}
}

function handle_menu_click(action) {
	let P = menu.under_menu.P;

	if (action == 'Remove') {
		remove_piece(P);
	}
	else if (action == 'Remove parent') {
		//register_child( P._main_parent._main_parent, P._main_parent._keyAsChild, P);
		if ( !['Program','BlockStatement'].includes(P._main_parent.type) 
		&& compatible( P._main_parent._main_parent, P, P._main_parent._keyAsChild ) )
			replace_node( P._main_parent, P);
	}
	else if (action == 'Remove operator') {
		let children = get_children(P).filter(C => C.type != 'Empty');
		if (children.length == 1 && compatible(P._main_parent, children[0], P._keyAsChild  ) )
			replace_node( P, children[0] );
	}
	else if (action == 'Disable' || action == 'Enable') {
		if (parent_is_array(P) ) register_child(P, 'disabled', !P.disabled);
	}
}


function find_empty_child(P) {
	for (let k in P) {
		if (isObject(P[k]) && P[k].type == 'Empty') return P[k];
	}
	return null;
}

function find_empty_children(P) {
	let res = [];
	for (let k in P) {
		if (isObject(P[k]) && P[k].type == 'Empty') res.push( P[k] );
	}
	return res;
}

let o = {
	ArrayExpression: 'elements',
	ObjectExpression: 'properties',
	BlockStatement: 'body',
	Program: 'body',
	CallExpression: 'arguments',
	NewExpression: 'arguments',
	FunctionExpression: 'params',
};

function contents_hidden(P) {
	return P.minimized;
}

function array_child(P) {
	for (let key in P) {
		if (key[0] != '_' ) {
			if (Array.isArray(P[key]) ) return {node: P, arr: key};
			if (['body','consequent'].includes(key) && P[key].type == 'BlockStatement' ) return {node: P[key], arr: 'body'};
		}
	}
	return null;
}

function move(M, D, copy) {
	if (D._parent === undefined) return;
	if (M == D) return;

	if (copy || M.is_sample) M = clone(M);

	let empty_children = find_empty_children(M);

	while (D._main_parent ) {
		if (D.type === 'Empty' && compatible(D._main_parent, M, D._keyAsChild) ) {
			M = edit_if_necessary(M, D._main_parent);
			separate_child(M);
			replace_node(D, M);
		}
		else if (M.type == 'Async' && D.type == 'FunctionExpression'  ) {
			Vue.set(D, 'async', !D.async);
		}
		else if (M.type == 'Async' && D.type == 'AssignmentExpression' && D.right.type == 'FunctionExpression' ) {
			Vue.set(D.right, 'async', !D.right.async);
		}
		// Make it possible to move an element inside a array ? That's it? Maybe delete
		else if ( ['ArrayExpression', 'BlockStatement', 'Program'].includes(D.type) && compatible(D, M) ) {
			let arr = D[o[D.type]];
			//if (arr.length > 0) return false;
			separate_child(M);

			let key = o[D.type];
			register_child_in_arr(D, key, D[key].length, M);
		}
		else if ( empty_children.length > 0 && compatible(D._main_parent, M, D._keyAsChild) ) {
			let empty_child;

			for (let c of empty_children) {
				if ( compatible(M, D, c._keyAsChild) ) {
					empty_child = c;
					break;
				}
			}

			if (!empty_child) return;

			separate_child(M);
			replace_node(D, M);
			replace_node(empty_child, D);
		}
		
		else if (['ObjectProperty', 'ConditionStatement'].includes(D.type) && compatible(D._main_parent, M, D._keyAsChild) ) {
			let new_array_index = D._parent.indexOf(D);
			//Make sure to add the element to the end
			if (new_array_index == D._parent.length - 1 && !D._parent.includes(M) ) new_array_index++; 
			separate_child(M);

			register_child_in_arr(D._main_parent, D._parent, new_array_index, M);
		}
		else {
			D = D._main_parent;
			if (is_block(D) ) return;
			else continue;
		}

		break;
	}

	if (M.is_sample) {
		delete M.is_sample;
	}

	unselect_all();
	return true;
}

Vue.prototype.$handle_menu_click = handle_menu_click;

function is_ancestor(X, Y) {
	let ancestor = Y;
	while ( ancestor !== undefined) {
		if (ancestor == X) return true;
		ancestor = ancestor._main_parent;
	}
	return false;
}

function is_a_loop(P) {
	return ['WhileStatement', 'ForOfStatement', 'ForRangeStatement'].includes(P.type);
}

function compatible(parent, child, keyAsChild) {
	let err;

	if (parent) {
		if ( is_ancestor(child, parent) )
			err = 'You cannot move the piece into itself';
		else if (child.type == 'Async' && parent.type != 'FunctionExpression') 
			err = 'Async can only be placed on a Function';
		else if (child.type == 'AwaitExpression' && !find_ancestor(parent, A => A.async == true ) )
			err = 'Await must be inside an AsyncFunction';
		else if (parent.type == 'MemberExpression'
			&& child.type == 'BinaryExpression' && child.is_boolean) 
			err = 'Boolean BinaryExpression cannot be a child of MemberExpression';
		else if (parent.type == 'BinaryExpression' && !parent.is_boolean
			&& (child.type == 'BinaryExpression' && child.is_boolean || child.type == 'BooleanLiteral'))
			err = 'Boolean expression cannot be a child of a math expression';
		else if (parent.type == 'CallExpression'
			&&  !['Identifier', 'MemberExpression', 'CallExpression', 'FunctionExpression'].includes(child.type)  && keyAsChild == 'callee' ) 
			err = child.type + ' is not a function';
		else if (child.type == 'ObjectProperty' && parent.type != 'ObjectExpression')
			err = 'Object property can only be inside an object';
		else if (child.type != 'ObjectProperty' && parent.type == 'ObjectExpression')
			err = 'Object can only have object property as a child';
		else if ( must_be_a_line(child) && !is_block(parent) )
			err = child.type + ' can only be in a block of statements';
		else if (child.type != 'StringLiteral' && parent.type == 'ObjectProperty' && keyAsChild == 'key')
			err = 'The key of an object property must be a string';
		else if (['BlockStatement', 'ElseStatement'].includes(child.type)  )
			err = child.type + ' is not movable';
		//else if (parent.type == 'FunctionExpression' && child.type != 'Identifier')
			//err = 'Argument must be an Identifier';
		else if (child.type == 'ConditionStatement' && parent.type != 'IfStatement')
			err = 'ConditionStatement must be inside of IfStatement'
		else if (child.type != 'ConditionStatement' && parent.type == 'IfStatement')
			err = 'IfStatement can only include ConditionStatement';
		else if (child.type == 'ReturnStatement') {
			let ancestor = parent;
			while (!['FunctionExpression', 'FunctionDeclaration', 'Program'].includes(ancestor.type) ) {
				ancestor = ancestor._main_parent;
			}
			if (ancestor.type == 'Program') err = 'ReturnStatement must be inside a Function';
		}
		else if ( ['BreakStatement','ContinueStatement'].includes(child.type) ) {
			let A = find_ancestor(parent, A => is_a_loop(A) );
			if (!A) err = child.type + ' must be inside a loop';
		}
		/*
		else if (child.type == 'ItselfLiteral') {
			let ancestor = parent;
			while (ancestor && ancestor.type != 'AssignmentExpression') {
				ancestor = ancestor._main_parent;
			}
			if (ancestor === undefined) err = 'Itself keyword can only be inside of AssignmentExpression';
		}*/
		else if (parent.type == 'AssignmentExpression' && keyAsChild == 'left' && !['Identifier', 'MemberExpression', 'ArrayExpression', 'ObjectExpression'].includes(child.type)  )
			err = 'Value being set must be Identifier or MemberExpression';
		/*
		else if (['Program','BlockStatement'].includes(parent.type)
			&& !['IfStatement','WhileStatement','ForOfStatement','CallExpression','AssignmentExpression'].includes(child.type) )
			err = 'You cannot make a line from ' + child.type;
		*/
		/*
		else if (child.type == 'Empty' )
			err = 'You cannot move Empty anywhere';
		*/

	}

	if (err === undefined) return true;
	//$g.console.warn(err);

	return false;
}

import AutosizeInput from 'vue-autosize-input';

let paste = {
	piece: null,
	type: null
};

let key_is_pressed = {Control: false};

let different_order = {
	'IfStatement': ['conditions', 'alternate']
};

function edit_if_necessary(M, D) {
	if (M.is_sample && is_block(D) && ['ArrayExpression','ObjectExpression','FunctionExpression'].includes(M.type) ) {
		delete M.is_sample;
		delete M.focus;
		let A = get_sample('Set');
		A.right = M;
		set_all_relationships(A);
		return A;
	}
	else return M;
}

function get_all_children(P) {
	let res = [];

	for (let k of different_order[P.type] || Object.keys(P) ) {
		if (k[0] != '_' && isObject(P[k]) && P[k].type) res.push(P[k]);
		else if (k[0] != '_' && Array.isArray(P[k]) ) res = res.concat(P[k]);
	}
	return res;
}

function get_first_child(P) {
	for (let k of different_order[P.type] || Object.keys(P) ) {
		if (k[0] != '_' && isObject(P[k]) && P[k].type) return P[k];
		else if (k[0] != '_' && Array.isArray(P[k]) ) return P[k][0];
	}
	return null;
}

function is_editable(P) {
	return ['Identifier', 'StringLiteral', 'NumericLiteral', 'Empty', 'Broken', 'Comment'].includes(P.type);
}

function unselectable(P) {
	//  this.P.type == 'BlockStatement' || this.P.type == 'Empty' || 
	return is_block(P)
		|| P._vm === undefined
		|| !P.is_sample && P._parent === undefined 
		||  P.type == 'ConditionStatement' && (P.is_sample || P._parent && P._parent.length == 1)
		// || this.P._main_parent.type == 'ObjectProperty' && this.P._keyAsChild == 'key'
}

/*
function get_last_editable(P) {
	let N, prev_editable;
	while (P) {
		if (is_editable(P) ) prev_editable = P;

		N = get_prev_child(P);
		if (prev_editable && is_editable(N) && N.value != '') return prev_editable;
		P = N;
	}
	return P;
}
*/

function get_last_editable(P) {
	while (P) {
		if (is_editable(P) ) return P;
		P = get_prev_child(P);
	}
	return null;
}


function get_first_empty_editable(P) {
	let all_leafs = get_all_leafs(P);
	let E;
	for (let leaf of all_leafs) {
		if (is_editable(leaf)) E = leaf;
		if (is_editable(leaf) && leaf.value != undefined && leaf.value.length == 0) return leaf;
	}
	return E;
}


function prev_ver(P) {
	return unselectable(P) ? get_prev_child(P) : P;
}

function get_prev_child(P) {
	if (!contents_hidden(P)) {
		let children = get_all_children(P);
		if (children.length > 0) return prev_ver( children[children.length-1] );
	}

	while(P._main_parent) {
		let siblings = get_all_children(P._main_parent);
		let index_as_child = siblings.indexOf(P);

		if (index_as_child > 0) {
			return prev_ver( siblings[index_as_child-1] );
		}
		P = P._main_parent;
	}

	return null;
}

function next_ver(P) {
	return unselectable(P) ? get_next_child(P) : P;
}


function get_next_child(P) {
	if (!P._main_parent) return null;

	let siblings = get_all_children(P._main_parent);
	let index_as_child = siblings.indexOf(P);

	if (index_as_child == siblings.length - 1) {
		return next_ver(P._main_parent);
	}

	let N = siblings[index_as_child + 1];
	if (contents_hidden(N)) return next_ver(N);//get_next_child(N);

	while(true) {
		let first_child = get_first_child(N);
		if (first_child == null) return next_ver(N);
		else N = first_child;
	}
}

function get_first_leaf(P) {
	while(true) {
		let first_child = get_first_child(P);
		if (first_child == null) return P;
		else P = first_child;
	}
}

function get_all_leafs(P) {
	let res = [];
	get_all_leafs_recursive(P, res);
	return res;
}

function get_all_leafs_recursive(P, res) {
	let children = get_all_children(P);
	if (children.length == 0) res.push(P);
	else for (let C of children) {
		get_all_leafs_recursive(C, res);
	}
}

function get_cur_line(P) {
	return find_ancestor(P, A => parent_is_array(A) && ['BlockStatement', 'Program', 'ArrayExpression', 'CallExpression'].includes(A._main_parent.type));
}

document.addEventListener("keypress", function(event) {
    if ( ['KeyZ','KeyY'].includes(event.code) && event.ctrlKey ) {
    	event.preventDefault();
    }
});

document.addEventListener("keydown", function(event) {
	key_is_pressed[event.code] = true;

	let E;
    if (event.code === "Escape") {
    	unselect_all();
    	document.activeElement.blur();
    }
    else if (event.code === 'Delete' && $g.selected_pieces.length) {
    	for (let p of $g.selected_pieces)
    		remove_piece(p);

    	unselect_all();
    	event.preventDefault();
    }
    else if (event.code === 'KeyZ' && event.ctrlKey ) {
    	document.activeElement.blur();
    	Remember.undo();
    	event.preventDefault();
    }
    else if (event.code === 'KeyY' && event.ctrlKey ) {
    	document.activeElement.blur();
    	Remember.redo();
    	event.preventDefault();
    }

	if (common.focused) return;

	let P = $g.selected_pieces.length == 1 ? $g.selected_pieces[0] : null;

	/*
	if (event.code == 'KeyE') {
    	//$g.selected_pieces = [ samples['Empty'] ];
    	select(samples['Empty']);
    } else*/
    if (event.code === 'KeyC' && event.ctrlKey ) {
    	common.paste_mode = 'copy';
    	event.preventDefault();
    }
    else if (event.code === 'KeyX' && event.ctrlKey ) {
    	common.paste_mode = 'cut';
    	event.preventDefault();
    }
	else if (event.code == 'ArrowLeft' && $g.selected_pieces.length == 1) {
		let S = get_prev_child($g.selected_pieces[0]) || $g.selected_pieces[0];
		select(S);
		event.preventDefault();
		event.stopPropagation();
	}
	else if (event.code == 'ArrowRight' && $g.selected_pieces.length == 1) {
		let S = get_next_child($g.selected_pieces[0]) || $g.selected_pieces[0];
		select(S);
		event.preventDefault();
		event.stopPropagation();
	}
    else if (event.code == 'KeyF' || event.code == 'Backspace') {
		if (P) {
			if (event.code == 'KeyF') Vue.set(P, 'focus', {position: 'end', child: 'start'} );
			else set_focus(P, 'end');

			unselect_all();
			event.preventDefault();
			event.stopPropagation();
		}
    }
    else if (event.code == 'KeyX') {
    	common.toggle_disabled();
    }
    else if (event.code == 'Enter' && $g.selected_pieces.length == 1) {
    	if (P) {
    		make_a_new_line(P);
    		unselect_all();
    		event.preventDefault();
    		event.stopPropagation();
    	}
    }
    else {
    	if (P && (E = operator_to_piece(event.key)) ) {
			move(E, P, true);
			event.preventDefault();
			event.stopPropagation();
    	}
    }

});

document.addEventListener("keyup", function(event) {
	key_is_pressed[event.code] = false;
});


function is_a_valid_identifier(s) {
	return s.match(/^[A-Za-z$_][A-Za-z$_0-9]*$/) !== null && !['if','for','continue','null', 'true', 'false'].includes(s);
}

function is_a_valid_number(s) {
	return s.match(/^-?[0-9]+(\.[0-9]*)?$/);
}

import Vue from 'vue'

let change_operator = {'==': '=', '>=':'', '<=': '', '!=':'', '*':'', '-':'', '&&':'and', '||':'or', '!':'not', '**':'^' };

function is_boolean(P) {
	return P.type == 'BinaryExpression' && ['==','!=','>','<','>=','<='].includes(P.operator);
}

function on_change_body_length() {
	if (is_block(this.P) && this.P.body.length == 0 && this.P._main_parent) {
		//move({type: 'Empty'}, this.P, true);
		register_child_in_arr(this.P, 'body', 0, {type: 'Empty'} );
	}
}

function on_change_type() {
	if (this.P.type == 'Empty' && this.P.value === undefined) {
		Vue.set(this.P, 'value', '' );
	}
	else if (this.P.type == 'NumericLiteral' ) {
		this.P.value += '';
	}
	else if (is_boolean(this.P) ) {
		Vue.set(this.P, 'is_boolean', true );
	}
	else if (this.P.disabled && !parent_is_array(this.P) ) {
		this.P.disabled = false;
	}

	if (!this.P.is_sample && this.P._main_parent && this.P.focus) {
		this.focus();
	}
}

function operator_to_piece(last) {
	let E;
	let piece_name = {
		':': 'Set',
		'~': 'Not',
		'!': 'CallExpression',
		'[': 'MemberExpression',
		'.': 'DotExpression',
	}[last];

	if (piece_name ) {
		E = get_sample(piece_name);
	}
	else if (['+','-','*','/','^','%','>','<','=','&','|'].includes(last) ) {
		last = {'=':'==','&':'&&','|':'||', '^':'**'}[last] || last;

		E = {
			type: ['&&','||'].includes(last) ? 'LogicalExpression': 'BinaryExpression',
			operator: last,
			left: {type: 'Empty'},
			right: {type: 'Empty' },
		};

		E.is_boolean = is_boolean(E);
	}

	if (E) E.focus = {child: 'start'};

	return E;
}

function select(P) {
	unselect_all();
	Vue.set(P, 'selected', true);
	$g.selected_pieces = [P];
}

function select_after_typing(P) {
	if (P.type == 'StringLiteral' && P._main_parent.type == 'MemberExpression' && P._keyAsChild == "property") {
		select(P._main_parent);
	}
	else select(P);
}

function is_empty_object_property(P) {
	return P.type == 'ObjectProperty' && P.key.value == '' && P.value.value == '';
}

function make_a_new_line(P) {
	let Empty = {type: 'Empty', focus: {} };

	if (P.type == 'Empty' && P._main_parent.type == 'Program') {
		return;
	}
	else if (is_empty_object_property(P._main_parent)) {
		separate_child(P._main_parent);
		P = P._main_parent._main_parent;
	}
	else if ( P._main_parent == 'FunctionExpression' && P._keyAsChild == 'params' ) {
		console.log('thats the one!');
	}
	else if ( parent_is_array(P) && P.type == 'Empty') {
		separate_child(P);
		P = P._main_parent;
	}


	while (P._main_parent) {
		let f = array_child(P._main_parent);
		let B;
		if (f) B = f.node[f.arr];
		
		if (B && P._parent != B && P.type != 'BlockStatement') {
			//console.log("DEEP ENTER!");
			//console.log({P,B});
			let please_replace = B[0] && B[0].type == 'Empty';
			register_child_in_arr(f.node, B, 0, Empty, please_replace);
			return;
		}
		else if (parent_is_array(P) && P._main_parent.type != 'IfStatement' ) {
			if (P._main_parent.type == 'ObjectExpression')
				add_new_property_to_obj(P._main_parent, P._parent.indexOf(P)+1);
			else {
				let i = P._parent.indexOf(P)+1;
				let please_replace = P._parent[i] && P._parent[i].type == 'Empty';
				register_child_in_arr(P._main_parent, P._parent, i, Empty, please_replace );
			}
			return;
		}
		P = P._main_parent;
	}
}

function get_value_by_key(P, key) {
	if (P.type == 'ObjectExpression') {
		let property = P.properties.filter(p => p.key.value == key)[0];
		return property && property.value;
	}
	else if (P.type == 'ArrayExpression') {
		return P.elements[key];
	}
}

function add_new_property_to_obj(P, index) {
	register_child_in_arr(P, 'properties', index, get_sample('ObjectProperty'));
}

function is_Keyword(P) {
	return P.type == 'Empty' && P.value[0] == '/';
}

function set_focus(P, position) {
	Vue.set(P, 'focus', {position} );
}

function calc_type(v) {
	if (v.length == 0 || v[0] == '/') {
		return 'Empty';
	}
	else if (is_a_valid_number(v) ) {
		return 'NumericLiteral';
	}
	else if (is_a_valid_identifier(v) ) {
		return 'Identifier';
	}
	else return 'Broken';
}

function is_block(P) {
	return ['BlockStatement','Program'].includes(P.type);
}

let Empty = {type: 'Empty'};

import contenteditable from '@/components/contenteditable'
import FunctionExpression from '@/components/FunctionExpression'

export default {
	name: 'Piece',
	components: {AutosizeInput, contenteditable, FunctionExpression},
	props: {
		P: Object,
		index_as_child: Number,
		need_brackets: Boolean,
		vertical_view: Boolean,
		placeholder: String,
		show_full: Boolean,
	},
	data: () => ({
		common,
		paste,
		key_is_pressed,
		dsadsads: menu,
		json_path:  [],
		hover: false,
		cur_block: false,
		show_minimize_btn: false,
		glow: {right: false, left: false},
	}),
	watch: {
		P(nv, ov) {
			//console.log("P changed!");
			this.init();
			//if (nv != ov) console.log(nv, ov);
		},
		'P.conditions.length': function(new_value) {
			if (new_value == 0) separate_child(this.P);
		},
		'P.body.length': on_change_body_length,
		'P.type' : on_change_type,
		'P.focus': function(new_value) {
			if (new_value) {
				this.focus();
			}
		},
		'P.selected': function(nv) {
			if (nv == true && this.P.is_sample) common.paste_mode = "copy";
		},
	},
	methods: {
		unselect_all,
		init() {
			this.P._vm = this;
			//Vue.set(this.P, 'selected', false);
			this.hover = false;
			this.cur_block = false;;
		},
		edit_operator(op) {
			return change_operator[op] || op;
		},
		handle_mouseover(event) {
			if (this.P.type == 'BlockStatement' && this.P._main_parent)
				this.show_minimize_btn = true;

			if (unselectable(this.P) ) return;
			event.stopPropagation();
			//if (this.$menu.showMenu) return;

			if (common.hover_P) common.hover_P._vm.hover = false;
			this.hover = true;
			common.hover_P = this.P;

			if (common.blue_hover) return;
			if (common.glow_side_P) common.glow_side_P._vm.glow = {left: false, right: false};

			let glow = {left: false, right: false};

			if (common.paste_mode && $g.selected_pieces.length == 1 && !this.is_sample ) {
				let M = $g.selected_pieces[0];
				let D = this.P;

				let empty_children = find_empty_children(M);
				let all_children = get_all_children(M);

				while (D._main_parent ) {
					if ( empty_children.length > 0 && compatible(D._main_parent, M, D._keyAsChild) ) {
						let empty_child;

						for (let c of empty_children) {
							if ( compatible(M, D, c._keyAsChild) ) {
								empty_child = c;
								break;
							}
						}

						if (!empty_child) return;
						
						if (all_children.length == 1) glow.left = true;
						else if (all_children.length > 1) glow.right = true;

						//$g.console.log(D);
						D._vm.glow = glow;
						common.glow_side_P = D;
						break;
					}

					D = D._main_parent;
					if (is_block(D) ) break;
				}
			}
		},
		handle_mouseleave() {
			//if (this.$menu.showMenu) return;
			if (this.show_minimize_btn) this.show_minimize_btn = false;
			common.hover_P = null;
			//if (common.glow_side_P) common.glow_side_P._vm.glow = {left: false, right: false};
			if (this.glow && (this.glow.left || this.glow.right) ) this.glow = {left: false, right: false};
			this.hover = false;
		},
		handle_click(event) {
			if (this.P.type == 'Empty' && !$g.selected_pieces.length && this.P._main_parent) {
				event.stopPropagation();
				return;
			}

			if (unselectable(this.P) ) return;

			let select_many = common.select_many || event.shiftKey;

			let moved_smth;
			if ($g.selected_pieces.length == 1 && !select_many && common.paste_mode) {
				moved_smth = move($g.selected_pieces[0], this.P, common.paste_mode == 'copy');
			}

			if (moved_smth) unselect_all();
			else if (this.P.type != 'Empty' || this.P.is_sample) {
				if (select_many) {
					let index = $g.selected_pieces.indexOf(this.P)
					if (index != -1) $g.selected_pieces.splice(index, 1);
					else {
						Vue.set(this.P, 'selected', true);
						$g.selected_pieces.push(this.P);
					}
				}
				else if ($g.selected_pieces.length == 1 && $g.selected_pieces[0] == this.P)
					unselect_all();
				else select(this.P);
			}

			event.stopPropagation();
		},
		handle_anchor(index, event) {
			this.insert_into_arr(this.P, index);
			event.preventDefault();
			event.stopPropagation();
		},
		insert_into_arr(P, index) {
			let sorted = common.IDE.sort_by_order($g.selected_pieces);

			for (let i=sorted.length-1; i>=0; i--) {
				let M = sorted[i];

				if (common.paste_mode == 'copy' || M.is_sample) M = clone(M);

				M = edit_if_necessary(M, P);

				if ( !compatible(P, M) ) return;

				let E;
				if (M._main_parent == P) {
					E = {type: 'Empty'};
					replace_node(M, E);
				}
				else separate_child(M);
				
				register_child_in_arr(P, o[P.type], index, M);
				if (E) separate_child(E);

				if (M.is_sample) delete M.is_sample;
			}
			unselect_all();
		},
		is_primitive(P) {
			return ['Identifier', 'StringLiteral', 'NumericLiteral', 'Empty', 'ItselfLiteral', 'ThisExpression'].includes(P.type);
		},
		maybe_brackets_for_term(P, child) {
			return P.operator == '**' && P[child].type && ['+','-','*','/'].includes(P[child].operator)
				|| P.operator == '**' && P[child].type && P[child].operator == '**'
				|| P.operator == '*' && P[child].type && ['+','-'].includes(P[child].operator)
				|| P.operator == '-' && (child == 'right' || child == 'argument') && P[child].type && ['+','-'].includes(P[child].operator);
		},
		open_menu(e) {
			if (unselectable(this.P) ) return;

			event.stopPropagation();
			event.preventDefault();

			menu.under_menu = this;
			menu.menu_position.x = e.clientX;
			menu.menu_position.y = e.clientY;
			menu.showMenu = true;
			//setTimeout( () => this.$menu.showMenu = true, 100);
		},
		add_new_property() {
			add_new_property_to_obj(this.P, this.P.properties.length);
		},
		add_new_element() {
			register_child_in_arr(this.P, 'elements', this.P.elements.length, get_sample('Empty') );
		},
		add_new_argument() {
			register_child_in_arr(this.P, 'arguments', this.P.arguments.length, get_sample('Empty') );
		},
		add_new_condition(index) {
			if (index === 0) {
				register_child_in_arr(this.P, 'conditions', this.P.conditions.length, get_sample('ConditionStatement') );
			}
			else {
				register_child(this.P, 'alternate', get_sample('Else') );
			}
		},
		add_new_param() {
			let I = {type: 'Identifier', value: '', focus: true };
			let o = this.P.params ? this.P : this.P.right;
			register_child_in_arr(o, 'params', o.params.length, I);
		},
		add_step() {
			register_child(this.P, 'step', {type: 'NumericLiteral', value: 1});
		},
		remove_step() {
			register_child(this.P, 'step', null);
		},
		get_view(child) {
			if (child.type == 'UnaryExpression') child = child.argument;

			if (child.type == 'LogicalExpression') {
				if (child.operator == this.P.operator) return this.vertical_view;
				else return !this.vertical_view;
			}
			return undefined;
		},
		bracket_vertical(child) {
			if (child.type == 'LogicalExpression') {
				if (child.operator == this.P.operator) return false;
				else return !this.vertical_view;
			}
			return undefined;
		},
		toggle_boolean() {
			if (!this.P.is_sample)
				register_child(this.P, 'value', !this.P.value);
		},
		get_id(P) {
			if (P) return P._id || (P._id = Math.random());
			else return 'a';
		},
		handle_input_focus() {
			common.focused = this.P;
			this.P.initial_input = this.P.value;
			this.P.initial_type = this.P.type;
		},
		handle_input_blur() {
			common.focused = null;
			if (this.P.initial_input != this.P.value ) {
				Remember.set(this.P, 'value', this.P.value, this.P.initial_input);
				Remember.set(this.P, 'type', this.P.type, this.P.initial_type);
			}
		},
		handle_change(event) {
			if ($g.selected_pieces.length)
				unselect_all();

			this.P.type = calc_type(this.P.value);

			event.stopPropagation();
		},
		handle_keydown(event) {
			let E;
			let v = this.P.value;

			if (event.code == 'Backspace' && v.length == 0) {
				//event.target.selectionStart == the position of the cursor inside input
				// if pressing backspace on Empty, remove it
				let prev = get_prev_child(this.P);

				if (prev) {
					set_focus(prev, 'end');
				}
				else {
					let next = get_next_child(this.P);
					if (next) set_focus(next, 0);
					else return;
				}

				if (parent_is_array(this.P) ) {
					remove_piece(this.P);
				}
				else {
					remove_operator(this.P._main_parent);
				}
				event.preventDefault();
				event.stopPropagation();
			}
			else if (event.code == 'ArrowRight' && event.target.selectionStart == v.length
				|| event.code == 'ArrowLeft' && event.target.selectionStart == 0) {
				this.handle_arrow_excess();
			}
			else if (['Enter','Tab'].includes(event.code) && this.is_Keyword ) {
				common.timeout_to_choose_first = setTimeout( () => {
					this.transform_from_keyword();
				},100);
			}
			else if (event.code == 'Enter') {
				make_a_new_line(this.P);
				event.preventDefault();
				event.stopPropagation();
			}
			else if (event.key == '"' && (this.caret_is_at('end') || this.caret_is_at(0))) {
				let new_piece = {
					type: 'StringLiteral',
					focus: this.caret_is_at(0) ? {} : undefined,
					value: this.P.value
				}
				if (compatible(this.P._main_parent, new_piece, this.P._keyAsChild)) {
					replace_node(this.P, new_piece);

					if (this.caret_is_at('end')) {
						//select_after_typing(new_piece);
						Vue.set(new_piece, 'selected', true);
						$g.selected_pieces = [new_piece];
					}
				}

				event.preventDefault();
				event.stopPropagation();
			}
			else if (event.code == "Space" && v[0] != '/') {
				event.preventDefault();
				event.stopPropagation();
			}
			else if (this.caret_is_at('end') ) {
				let last = event.key;

				let E;
				if (v[0] == '/') {
					this.open_keyword_menu();
				}
				else if ('-/'.includes(last) && v.length == 0
					|| last == '.' && this.P.type == 'NumericLiteral') {
					//do nothing; solves the negative number problem
				}
				else if (E = operator_to_piece(last) ) { 
					move(E, this.P, true);
					event.preventDefault();
					event.stopPropagation();
				}
			}
		},
		open_keyword_menu() {
			common.show_keyword_menu = true;
			common.el_with_keyword_menu = this;
		},
		caret_is_at(pos) {
			let I = this.dom_input();
			if (pos == 'end') pos = I.value.length;
			return I.selectionStart == pos && I.selectionEnd == pos;
		},
		handle_arrow_excess() {
			document.activeElement.blur();
			if (event.code == 'ArrowRight') {
				select_after_typing(this.P);
			}
			else select(this.P);
			
			event.stopPropagation();
		},
		handle_backspace_at_beginning(event) {
			//called by contenteditable (strings)
			unselect_all();

			replace_node(this.P, {
				type: calc_type(this.P.value),
				focus: {},
				value: this.P.value
			});
		},
		show_anchor(i) {
			if (this.$vuetify.breakpoint.mdAndUp) return false;
			if (!($g.selected_pieces.length && common.paste_mode && this.P._main_parent)) return false;
			if (key_is_pressed.Control || common.paste_mode == 'copy') return true;

			for (let p of $g.selected_pieces) {
				if ( [null, this.arr[i-1], this.arr[i-2]].includes(p) ) return false;
			}
			return true;
		},
		smart_index(i) {
			let res = this.arr_without_disabled.indexOf(this.P.elements[i]);
			return res == -1 ? '-- ' : res;
		},
		focus() {
			if (this.P.is_sample) return;

			let focus_position = this.P.focus && this.P.focus.position;

			if (is_editable(this.P) ) this.$nextTick(() => {
				if (this.$refs.input_field) {
					this.$refs.input_field.focus();

					if (focus_position != undefined) {
						let caret;
						if (focus_position == 'end') caret = this.P.value.length;
						else caret = focus_position;
						this.dom_input().setSelectionRange(caret, caret);
					}
				}
				else if (this.$refs.CE) {
					this.$refs.CE.focus();
				}
				delete this.P.focus;
			});

			else {
				let leaf;
				if (this.P.focus.child == 'start') {
					leaf = get_first_empty_editable(this.P);
				}
				else leaf = get_last_editable(this.P);

				if (leaf) set_focus(leaf, 'end');
				else select(this.P);
				delete this.P.focus;
			}
		},
		transform_from_keyword(piece_type) {
			if (!piece_type) piece_type = this.keyword_options[0].piece_type;
			move(samples[piece_type], this.P, true);
			common.show_keyword_menu = false;
		},
		handle_keydown_CE(event) {
			if (event.code == 'Enter' && !event.shiftKey) {
				make_a_new_line(this.P);
				event.preventDefault();
				event.stopPropagation();
			}
		},
		toggle_minimize_block() {
			if (this.P.minimized) Remember.set(this.P, 'minimized', undefined);
			else Remember.set(this.P, 'minimized', true);
		},
		dom_input() {
			return this.$refs.input_field.$el.children[0];
		},
		cut_path(index, event) {
			if (index < this.json_path.length) this.json_path = this.json_path.slice(0,index);
		},
		folder_click() {
			let path_bit, cur_folder;

			if (this.P._main_parent.type == 'ObjectProperty') {
				path_bit = this.P._main_parent.key.value;
				cur_folder = this.$parent.$parent.$parent;
			}
			else if (this.P._main_parent.type == 'ArrayExpression') {
				path_bit = this.P._main_parent.elements.indexOf(this.P);
				cur_folder = this.$parent.$parent;
			}
			else console.error("omg smth went terribly wrong");
			//console.log({path_bit, cur_folder});

			let head_unit;
			if (cur_folder.$parent.P && ['ObjectExpression', 'ArrayExpression'].includes(cur_folder.$parent.P.type)) {
				head_unit = cur_folder.$parent;
			}
			else head_unit = cur_folder;

			head_unit.json_path.push(path_bit);
		},
		toggle_finally() {
			if (!this.P.finalizer) register_child(this.P, 'finalizer', {type: 'BlockStatement', body: [] });
			else this.P.finalizer = null;
		},
		handle_blue_click(position) {
			event.stopPropagation();
			let cur_line = get_cur_line(this.P);
			let insert_index = cur_line._parent.indexOf(cur_line) + (position == 'bottom' ? 1: 0);
			if ($g.selected_pieces.length > 0 && common.paste_mode) this.insert_into_arr(cur_line._main_parent, insert_index);
			else register_child_in_arr(cur_line._main_parent, cur_line._parent, insert_index, get_sample('Empty'));
		},
		show_blue(position) {
			if (this.$vuetify.breakpoint.xs || !this.hover) return false;
			if (position == 'vertical') {
				return true;
			}
		},
		handle_blue_mouseover() {
			common.blue_hover = true;
			if (common.glow_side_P) {
				common.glow_side_P._vm.glow = {left: false, right: false};
			}
		},
		handle_blue_mouseleave() {
			common.blue_hover = false;
		},
		test(...args) {
			console.log(...args);
			return true;
		}
	},
	computed: {
		piece_name() {
			return {'ObjectExpression': 'Object', 'ArrayExpression': 'List'}[this.P.type];
		},
		is_child_in_json() {
			return this.P._main_parent && ['ObjectProperty', 'ArrayExpression'].includes(this.P._main_parent.type);
		},
		gotta_show_full() {
			return this.show_full || (!this.is_child_in_json && this.json_path.length == 0) || this.P.is_sample;
		},
		op_list() {
			return this.P.is_boolean ? ['==','!=','>','<','<=','>='] : ['+','-','*','/','%'];
		},
		arr() {
			return this.P[o[this.P.type]];
		},
		mark() {
			return this.hover;
			//if (menu.showMenu) return menu.under_menu == this;
			//return hover_P == this.P;
		},
		elseif_else_arr() {
			return this.P.alternate ? ['Else if'] : ['Else if','If none of the above'];
		},
		brackets() {
			return this.need_brackets // || this.P.type == 'SpreadElement'
			|| this.P.type == 'LogicalExpression' && this.P._main_parent && !['ConditionStatement','LogicalExpression'].includes(this.P._main_parent.type);
		},
		arr_without_disabled() {
			return this.P.elements.filter(e => !e.disabled);
		},
		is_Keyword() {
			return this.P.type == 'Empty' && this.P.value && this.P.value[0] == '/';
		},
		cur_keyword() {
			return this.P.value.slice(1);
		},
		keyword_options() {
			if ( this.is_Keyword && this.cur_keyword.length > 0) {
				return find_by_keyword(this.cur_keyword);
			}
		},
		cur_folder() {
			let res = this.P;
			for (let i=0; i < this.json_path.length; i++) {
				let key = this.json_path[i];

				let next = get_value_by_key(res, key);
				if (next) res = next;
				else {
					console.log("FIXING THE PATH!");
					this.json_path = this.json_path.slice(0,i-1);
					break;
				}
			}

			return res;
		},
	},
	created() {
		this.init();
	},
	mounted() {
		//this.$nextTick(() => {
			on_change_type.call(this);
			on_change_body_length.call(this);
		//});
	},
	updated() {
		//if (this.P.value) $g.console.log(this.P.type + ': ' + this.P.value);
		//else $g.console.log(this.P.type);
		//$g.console.log("updated!");
	}
}
</script>

<style>

.piece {
	xcolor: #55d4db;
}

.list-complete-item {
	transition: all 0.3s;
}

.list-complete-enter, .list-complete-leave-to {
	opacity: 0;
	xtransform: translateY(30px);
}

.list-complete-leave-active {
	position: absolute;
}

.list-complete-move {
	transition: transform 0.3s;
}


.slide-enter-active {
   transition-duration: 0.5s;
   transition-timing-function: linear;
}

.slide-leave-active {
   transition-duration: 0.5s;
   transition-timing-function: linear;
}

.slide-enter-to, .slide-leave {
   max-height: 100px;
   overflow: hidden;
}

.slide-enter, .slide-leave-to {
   overflow: hidden;
   max-height: 0;
}

.line_anchor {
	display: flex;
	justify-content: center;
	align-items: center;
	min-width: 20px;
	height: 20px;
	color: black;
	border-radius: 5px;
	margin: 5px;
	position: absolute;
	left: -20px;
	top: -15px;
	z-index: 2;
	background-color: white;
	opacity: 0.4;
	font-size: 20px;
	padding-bottom: 4px;
}

.line_with_anchor, .posrel {
	position: relative;
}

.hover {
	background-color: #444444 !important;
	background: none;
}

.NumericLiteral, .String, .Identifier, .Null, .Empty, .Broken {
	padding: 2px 8px;
	margin: 3px;
	border-radius: 10px;
	xborder: 1px solid black;
	cursor: default;
    color: #fff;
    font-weight: bold;
    xposition: relative;
    white-space: nowrap;
}

.theme-textstyle .NumericLiteral, .theme-textstyle .String, .theme-textstyle .Identifier, .theme-textstyle .Null {
	margin: 0px;
	padding: 0px 4px;
}

.theme-textstyle .Empty, .theme-textstyle .Broken {
	margin: 2px;
	height: 1px;
}

.theme-textstyle .Empty input, .theme-textstyle .Broken input {
	width: 10px !important;
	xfont-size: 15px !important;
}

.theme-main .Null {
	background: linear-gradient(180deg, hsl(180, 70%, 40%) 0%, hsl(180, 70%, 10%) 66%);
}

.theme-main .NumericLiteral {
	background: linear-gradient(180deg, hsl(120, 40%, 80%) 0%, hsl(120, 40%, 20%) 66%);
}

.theme-main .String {
	background: linear-gradient(180deg, hsl(50, 40%, 60%) 0%, hsl(50, 40%, 15%) 66%);
}

.theme-main .Identifier {
	background: linear-gradient(180deg, hsl(330, 70%, 40%) 0%, hsl(330, 70%, 15%) 66%);
}

.theme-textstyle .NumericLiteral * {
	color: hsl(120, 60%, 60%);
}

.theme-textstyle .String * {
	color: hsl(50, 40%, 40%);
}

.theme-textstyle .Identifier * {
	color: hsl(330, 70%, 40%);
}


.Unit {
	background: linear-gradient(180deg, rgb(40, 40, 40, 0.5) 0%, rgba(10, 10, 10, 0.5) 100%);
	border: 1px solid black !important;
	border-radius: 10px;
	margin: 4px;
}

.ObjectExpressionCap, .ArrayExpressionCap {
	border-top-left-radius: 10px;
	border-top-right-radius: 10px;
	margin-bottom: 4px;
	color: #fff;
}

.ObjectExpressionCap {
	background: linear-gradient(175deg, rgb(65, 177, 67) 0%, rgb(10, 52, 50) 66%);
}

.ArrayExpressionCap {
	background: linear-gradient(175deg, rgb(54, 114, 255) 0%, rgb(0, 20, 81) 66%);
}

.selected {
	background: #ada85c !important;
}

.block {
	margin-left: 15px;
	padding-left: 15px;
	border-left: 1px solid hsl(180, 30%, 25%);
	border-radius: 8px;
}

.cur_block {
	border-left: 1px solid orange;
}

.xblock {
	padding-left: 10px;
	padding-right: 10px;
}

.block_margin {
	margin-bottom: 20px;
}

.quotes {
	color: #111111;
	font-weight: bold;
}

.theme-textstyle .quotes {
	color: white !important;
}

.piece {
	display: inline-block;
	border-radius: 5px;
	xborder: 5px solid rgba(0,0,0,0);
}

.piece > * {
	display: inline-block;
}

.half_height, .full_height {
	width: 1px;
	background-color: grey;
	overflow-x: auto;
}

.half_height {
	height: 50%;
}

.full_height {
	height: 100%;
}

.empty_line {
	width: 1px;
	height: 100%;
	display: flex;
}

.hor_con {
	height: 1px;
	width: 10px;
	background-color: grey;
}

.brackets {
	border: 1px solid white;
	border-radius: 5px;
	xmargin: 5px;
}

.theme-main {
	xborder: 1px solid white;
}

.theme-textstyle .brackets {
	xborder-radius: 5px;
	xborder-left: 2px solid white;
	xborder-right: 2px solid white;
}

.dont_ignore_spaces {
	white-space: pre;
}

textarea, input {
	outline: none;
	z-index: -1;
}

.autosize-input {
	margin-right: -2px
}

.theme-main .autosize-input > input {
	color: white;
}

.btn_add_property {
	width: 20px;
	font-size: 13px;
