import Vue from 'vue'
import Remember from '@/remember'

let common = {
	focused: null,
	hover_P: null,
	glow_side_P: null,
	blue_hover: false,
	paste_mode: null,
	select_many: false,
	cur_block: null,
	el_with_keyword_menu: null,
	show_keyword_menu: false,
	panel_drawer: false,
};

function isObject(P) {
	return typeof P == 'object' && !Array.isArray(P) && P != null;
}

function tree_walk(P, before, after) {
	if (typeof P != 'object' || P == null) return;

	if (before) before(P);

	for (let k in P) {
		if (k[0] == '_') continue;
		if (Array.isArray( P[k] )  ) {
			for (let e of P[k].slice(0)) tree_walk(e, before, after);
		}
		else tree_walk( P[k], before, after);
	}

	if (after) after(P);
}

function separate_child(P) {
	deal_with_focused();

	if (parent_is_array(P)) {
		Remember.remove(P._parent, P._parent.indexOf(P) );
	}
	else if (P._parent) register_child(P._parent, P._keyAsChild, {type: 'Empty'});
}

function parent_is_array(P) {
	return Array.isArray(P._parent);
}

function register_child(parent, keyAsChild, child) {
	delete child.is_sample;
	Remember.set(parent, keyAsChild, child);
	if (isObject(child) ) {
		Remember.set(child, '_parent', parent);
		Remember.set(child, '_main_parent', parent);
		Remember.set(child, '_keyAsChild', keyAsChild);
	}
}

function register_child_in_arr(main_parent, arr, index, child, please_replace) {
	delete child.is_sample;
	let parent = typeof arr == 'string' ? main_parent[arr] : arr;
	Remember[please_replace ? 'set' : 'add'](parent, index, child);
	if (isObject(child)) {
		Remember.set(child, '_parent', parent);
		Remember.set(child, '_main_parent', main_parent);
	}
}

function replace_node(P, N, fix) {
	deal_with_focused();
	
	if (fix) set_all_relationships(N);
	if ( parent_is_array(P) ) {
		register_child_in_arr(P._main_parent, P._parent, P._parent.indexOf(P), N, true);
	}
	else register_child( P._main_parent, P._keyAsChild, N );
}

function deal_with_focused() {
	if (common.focused) {
		document.activeElement.blur();
		Remember.finish_action();
	}
}


function clone(P, no_relationships) {
	if ( isObject(P) ) {
		//console.log("NEW OBJECT: ", P);
		let res = {};
		for (let k in P) {
			if (k[0] == '_' || ['loc','start','end','extra','selected'].includes(k) ) continue;

			if (Array.isArray(P[k]) ) {
				res[k] = [];
				for (let e of P[k]) {
					//console.log("ARRAY ELEMENT CLONING:", e);
					let C = clone(e, no_relationships);
					if (!no_relationships) {
						C._main_parent = res;
						C._parent = res[k];
					}
					res[k].push(C);
				}
			}
			else {
				//console.log("Cloning:", k,P[k] );
				res[k] = clone(P[k], no_relationships);
				if (!no_relationships && isObject( res[k] ) ) {
					res[k]._main_parent = res[k]._parent = res;
					res[k]._keyAsChild = k;
				}
			}
		}
		return res;
	}
	else return P;
}


function set_all_relationships(P) {
	if ( isObject(P) ) {
		for (let k in P) {
			if (k[0] == '_' ) continue;
			if (Array.isArray(P[k]) ) {
				for (let e of P[k]) {
					set_all_relationships(e);
					e._main_parent = P;
					e._parent = P[k];
				}
			}
			else {
				set_all_relationships(P[k]);
				if ( isObject( P[k] ) ) {
					P[k]._main_parent = P[k]._parent = P;
					P[k]._keyAsChild = k;
				}
			}
		}
	}
}

function remove_piece(P) {
	/*
	if (P.type == 'ConditionStatement' && P._parent.length == 1 ) {
		console.warn("You cannot remove the only ConditionStatement");
	}*/
	if (P.type == 'BlockStatement' || P.type == 'Program') {
		console.warn("You cannot remove " + P.type);
	}
	else if (P.type == 'ElseStatement') {
		P._parent.alternate = null;
	}
	else if (P.type == 'Empty' && ['BlockStatement','Program'].includes(P._main_parent.type) && P._parent.length == 1) {
		return;
	}
	//else separate_child(P);
	else if (P.type == 'Empty' || parent_is_array(P) ) separate_child(P);
	else replace_node(P, {type: 'Empty', focus: true});
}

function must_be_a_line(P) {	
	return ['AssignmentExpression', 'IfStatement', 'ForRangeStatement', 'ForOfStatement', 'WhileStatement', 'PrintStatement', 'ReturnStatement', 'BreakStatement', 'ContinueStatement', 'GlobalVariableStatement', 'Comment', 'Separator'].includes(P.type);
}

function find_ancestor(P, check) {
	while(P._main_parent) {
		if (check(P)) return P;
		P = P._main_parent;
	}
	return null;
}



$g = {};

function unselect_all() {
	for (let P of $g.selected_pieces) {
		P.selected = false;
	}
	$g.selected_pieces = [];
	common.paste_mode = null;
}


let menu = {
	showMenu: false,
	under_menu: null,
	menu_position: {x: 0, y: 0},
};




let $g = {
	selected_pieces: [],
};

export {common, tree_walk, separate_child, remove_piece, register_child, register_child_in_arr, replace_node, clone, set_all_relationships, isObject, must_be_a_line, menu, parent_is_array, find_ancestor, unselect_all, $g};