<template><div>
	<div class="panel_collision_model"> </div>
	<div class="panel">
		<div class="d-flex justify-center pa-1"> PANEL </div>
		<div class="d-flex justify-space-around tabs">
			<div
				v-for="(t,index) in ['Basic', 'Complex', 'List'] "
				:class="{chosen: cur_tab == index, 'd-flex justify-center pa-1':true}"
				@mousedown="cur_tab=index"
			> {{t}} </div>
		</div>
		<div class="d-flex justify-space-around tabs">
			<div
				v-for="(t,index) in ['Function','Math','Logic'] "
				:class="{chosen: cur_tab == index+3, 'd-flex justify-center pa-1':true}"
				@mousedown="cur_tab=index+3"
			> {{t}} </div>
		</div>
		<div v-for="P in panel_samples[cur_tab]" class="pl-1">
			<Piece :key="get_id(P)" :P="P" />
		</div>
	</div>
</div></template>


<script>

import {$g, common, tree_walk, remove_piece, register_child, replace_node, separate_child, clone, set_all_relationships, must_be_a_line, menu, parent_is_array, find_ancestor, unselect_all}
from '@/methods'
import samples from '@/samples.js'

export default {
	data: () => ({
		cur_tab: 0,
		common,
		$g,
		panel_samples: samples.panel_samples,
	}),
	methods: {
		get_id(P) {
			if (P) return P._id || (P._id = Math.random());
			else return 'a';
		},
	}
}
</script>

<style>

.panel {
	position: fixed;
	height: 100%;
	border-right: 1px solid black;
}

.panel, .panel_collision_model {
	width: 200px;
}

.v-navigation-drawer .panel, .v-navigation-drawer .panel_collision_model {
	width: 100%;
}

.panel > span {
	display: block;
}

</style>